import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des criticités
export default function Addcriticite() {

     //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { criticiteId } = useParams();
    const [criticiteData, setcriticiteData] = React.useState({ criticite: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

     //Méthode pour vérifier que la criticité n'existe pas déjà
    useEffect(() => {
        if (criticiteId) {
            axios.get(`/criticites/${criticiteId}`).then(response => {
                console.log(response.data);
                setcriticiteData({
                    criticite: response.data.criticite,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [criticiteId])

    //Méthode pour l'enregistrement de la criticité
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(criticiteData);
        if (criticiteId) {
            axios.put(`/criticites/${criticiteId}`, {
                ...criticiteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/Listcriticite')
            })
        } else {
            axios.post('/criticites', {
                ...criticiteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/Listcriticite')
            })
        }
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...criticiteData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setcriticiteData(currentState);
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...criticiteData };
        currentState[event.target.name] = event.target.value;
        setcriticiteData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une criticite</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">criticite</label>
                        <input type="text" name="criticite" value={criticiteData.criticite} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={setcheckboxData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

