import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des priorités
export default function ListPriorite() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [priorites, setPrioriteData] = React.useState([]);
    const fetchPriorite = () => {
        axios.get('/priorites').then(response => {
            setPrioriteData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setPrioriteData([
            axios.get('/priorites').then(response => {
                setPrioriteData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (prioriteId) => {
        axios.delete(`/priorites/${prioriteId}`).then(response => {
            fetchPriorite();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Priorités</h4>
        {priorites.length === 0 ? "Pas de priorite disponible" : null}
        <Link to="/AddPriorite" className="btn btn-outline-info btn-sm m-3">Ajouter une priorite</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>priorite</th>
                        <th>Accronyme</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {priorites.map(priorite => (
                        <tr key={priorite.id}>
                            <td>{priorite.id}</td>
                            <td>{priorite.accrPriorite}</td>
                            <td>{priorite.priorite}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{priorite.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddPriorite/${priorite.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(priorite.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

