import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des CSP
export default function ListCsp() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [csps, setCspData] = React.useState([]);
    const fetchCsp = () => {
        axios.get('/csps').then(response => {
            setCspData(response.data)
            console.log(response.data);
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setCspData([
            axios.get('/csps').then(response => {
                setCspData(response.data)
                console.log(response.data);
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (cspId) => {
        axios.delete(`/csps/${cspId}`).then(response => {
            fetchCsp();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Csp</h4>
        {csps.length === 0 ? "Pas de csp disponible" : null}
        <Link to="/AddCsp" className="btn btn-outline-info btn-sm m-3">Ajouter une csp</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>csp</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                     {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {csps.map(csp => (
                        <tr key={csp.id}>
                            <td>{csp.id}</td>
                            <td>{csp.csp}</td>
                               {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{csp.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddCsp/${csp.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(csp.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

