import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des contrats
export default function AddContrat() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur.
    let { contratId } = useParams();
    const [contratData, setContratData] = React.useState({ contrat: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

      //Méthode pour vérifier que le contrat n'existe pas déjà
    useEffect(() => {
        if (contratId) {
            axios.get(`/contrats/${contratId}`).then(response => {
                console.log(response.data);
                setContratData({
                    contrat: response.data.contrat,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [contratId])

    //Méthode pour l'enregistrement du contrat
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(contratData);
        if (contratId) {
            axios.put(`/contrats/${contratId}`, {
                ...contratData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListContrat')
            })
        } else {
            axios.post('/contrats', {
                ...contratData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListContrat')
            })
        }
    }

  // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...contratData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setContratData(currentState);
    }

        //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...contratData };
        currentState[event.target.name] = event.target.value;
        setContratData(currentState);
    }

      //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter un contrat</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Contrat</label>
                        <input type="text" name="contrat" value={contratData.contrat} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={contratData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <div className="d-flex justify-content-end mt-3">
                                <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

