import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des fiches suivi de santé
export default function AddFicheSuiviSante() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { ficheSuiviSanteId } = useParams();
    const [ficheSuiviSanteData, setFicheSuiviSanteData] = useState({ ficheSuiviSante: '' });
    const [typeData, setTypesATData] = useState([]);
    // const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que la fiche de suivi de santé n'existe pas déjà
    useEffect(() => {
        axios.get('/typeAts').then(response => {
            setTypesATData(response.data)
            console.log("type data");
            console.log(response.data);
        })
        if (ficheSuiviSanteId) {
            axios.get(`/ficheSuiviSantes`).then(response => {
                console.log(response.data);
                setFicheSuiviSanteData(response.data)
            })
        }
    }, [ficheSuiviSanteId])

      //Méthode pour l'enregistrement de la fiche de suivi de santé
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(ficheSuiviSanteData);
        if (ficheSuiviSanteId) {
            axios.put(`/ficheSuiviSantes/${ficheSuiviSanteId}`, {
                ...ficheSuiviSanteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListFicheSuiviSante')
            })
        } else {
            axios.post('/ficheSuiviSantes', {
                ...ficheSuiviSanteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListFicheSuiviSante')
            })
        }
    }

       //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...ficheSuiviSanteData };
        currentState[event.target.name] = event.target.value;
        setFicheSuiviSanteData(currentState);
    }

      //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-1">Ajouter une fiche Suivi Sante</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-6">
                    <form onSubmit={onSubmit}>
                        <div className="d-flex justify-content-start ">
                            <div className="m-1" >
                                <label >Salarié</label>
                                <input type="text" name="ficheSuiviSante" onChange={handleChange} className="form-control" />
                            </div>
                            <div className="m-1">
                                <label className="">Poste de travail</label>
                                <input type="text" name="ficheSuiviSante" onChange={handleChange} className="form-control" />
                            </div>
                        </div>
                        <div className="d-flex justify-content-start ">
                            <div className="m-1">
                                <label>Médecin du travail</label>
                                <input type="text" name="dateDeCreation" onChange={handleChange} className="form-control" />
                            </div>
                            <div className="m-1" >
                                <label> Fait le</label>
                                <input type="datetime-local" name="dateDeCreation" onChange={handleChange} className="form-control" />
                            </div>

                        </div>
                        <div className="m-1">
                            <label>Type d'arret </label>
                            <select name="typeAT" value={typeData.typeAt} className="form-control">
                                {typeData.map(typeAt => (
                                    <option value={typeData.id} key={typeAt.id} > {typeAt.typeAt}</option>
                                ))}
                            </select>
                        </div>
                        <div className="d-flex justify-content-start ">
                            <div className="m-1">
                                <label> Date début de l'arret</label>
                                <input type="datetime-local" name="datededebutAT" onChange={handleChange} className="form-control" />
                            </div>
                            <div className="m-1">
                                <label> Date de fin de l'arret</label>
                                <input type="datetime-local" name="datedefinAT" onChange={handleChange} className="form-control" />
                            </div>
                        </div>
                        <div className="ml-1 m-r-1">
                            <label> Proposition du médecin</label>
                            <textarea type="text" name="" onChange={handleChange} className="form-control" />
                        </div>
                        <div className="d-flex justify-content-end m-1">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

