import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des csp
export default function AddCsp() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { cspId } = useParams();
    const [cspData, setCspData] = useState({ csp: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

      //Méthode pour vérifier que la csp n'existe pas déjà
    useEffect(() => {
        if (cspId) {
            axios.get(`/csps/${cspId}`).then(response => {
                console.log(response.data);
                setCspData({
                    csp: response.data.csp,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [cspId])

    //Méthode pour l'enregistrement de la csp
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(cspData);
        if (cspId) {
            axios.put(`/csps/${cspId}`, {
                ...cspData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListCsp')
            })
        } else {
            axios.post('/csps', {
                ...cspData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListCsp')
            })
        }
    }

     //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...cspData };
        currentState[event.target.name] = event.target.value;
        setCspData(currentState);
    }

// Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...cspData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setCspData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une csp</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Csp</label>
                        <input type="text" name="csp" value={cspData.csp} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={cspData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

