import React from "react";
import { Row, } from "react-bootstrap";
import { Link } from "react-router-dom";

//Classe contenant la page d'accueil
const Home = () => {

  return (
    <>
      <Row className="d-flex flex justify-content-center">
        <Link to={"AtChoix"} className="col-3 m-3 btn btn-success p-5">créer Rapport AT</Link>
        <Link to={"addPQA"} className="col-3 m-3 btn btn-success p-5">créer PQA/SD</Link>
        <Link to={"addAutreRapports"} className="col-3 m-3 btn btn-success p-5">créer un rapport divers</Link>
      </Row>
      <Row className="d-flex flex justify-content-center">
        <Link to={"ListAction"} className="col-2 m-3 btn btn-success p-5">Consulter plan d'actions</Link>
        <Link to={"ListRapport"} className="col-2 m-3 btn btn-success p-5">Consulter liste des rapports</Link>
        <Link to={"ListActionFiltrer"} className="col-2 m-3 btn btn-success p-5">Consulter action mois en cours et dépassées</Link>
        <Link to={"HomeStat"} className="col-2 m-3 btn btn-success p-5">Consulter statistique</Link>
      </Row>
    </>
  );
};

export default Home;
