import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave, } from "@fortawesome/free-solid-svg-icons";
import moment from "moment";
import Select from "react-select";

// Classe pour l'ajout d'une action
export default function AddAction({ userInfo, setUserInfo }) {
    const location = useLocation();
    let { actionId } = location.state;

    // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    const [today, setToday] = useState(new Date());
    const [users, setUserData] = React.useState([]);
    const [priorites, setPrioriteData] = React.useState([]);
    const [criticites, setCriticiteData] = React.useState([]);
    const [correctPrevents, setCorrectPreventData] = React.useState([]);
    const history = useHistory();
    const [showDelai, setDelai] = useState(false);

    //Constantes servant à réveler et masquer les champs optionnels
    const [showPVRedacteur, setPVRedacteur] = useState(false);
    const [showPVPilote, setPVPilote] = useState(false);
    const [showPVPorteur, setPVPorteur] = useState(false);
    const [showPVValidateur, setPVValidateur] = useState(false);
    const [showModEtape, setModEtape] = useState(false);

    //Constantes servant à gérer les focus des différents input du formulaire
    const [autoFocusActionImmediat, setAutoFocusActionImmediat] = useState();
    const [autoFocusDelai, setAutoFocusDelai] = useState();
    const [autoFocusTitre, setAutoFocusTitre] = useState();
    const [autoFocusDescription, setAutoFocusDescription] = useState();
    const [autoFocusEtat, setAutoFocusEtat] = useState();
    const [autoFocusPilote, setAutoFocusPilote] = useState();
    const [autoFocusPorteur, setAutoFocusPorteur] = useState();
    const [autoFocusPriorite, setAutoFocusPriorite] = useState();
    const [autoFocusCriticite, setAutoFocusCriticite] = useState();
    const [autoFocusPJ, setAutoFocusPJ] = useState();
    const [autoFocusCorrectPrevent, setAutoFocusCorrectPrevent] = useState();

    //Constantes employées pour la gestion des rôles, du cycle de vie de l'action et des utilisateurs concernés par l'action
    const [quelleEtape, setEtape] = useState();
    const [quelPilote, setPilote] = useState(null);
    const [quelPorteur, setPorteur] = useState(null);
    const [pilotes, setPiloteData] = React.useState([]);

    //Constantes employées pour la gestion des inputs en format date
    const [actionData, setActionData] = React.useState({ delai: new Date(), rapport: actionId });
    const toggleDelai = () => { setDelai(!showDelai) };

    //Constante utilisée pour mettre la date du jour par défaut dans les inputs en format date
    const setInitialDate = (date) => {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2)
            month = '0' + month;
        if (day.length < 2)
            day = '0' + day;

        var m = [year, month, day].join('-');

        var dateDefault = moment().format(m)

        return dateDefault;
    }

    // Méthode pour afficher la fenêtre d'action en fonction du rôle de l'utilisateur
    useEffect(() => {

        if (userInfo.role === 1 || userInfo.role === 4) {
            setModEtape(true);
        }
        if (userInfo.role === 5) {
            setPVValidateur(true);
        }
        if (userInfo.role === 2) {
            setPVRedacteur(true);
        }
        if (userInfo.role === 3) {
            setPVPilote(true);
        }
    }, [userInfo])

    //Méthode pour afficher dans les inputs format select correspondant les valeurs récupérées depuis la BDD
    useEffect(() => {
        if (quelPilote != null)
            setActionData(actionData => ({ ...actionData, pilote : quelPilote }));

        if (quelPorteur != null) {
            setActionData(actionData => ({ ...actionData, porteur: quelPorteur }));
        }
    }, [quelPilote, quelPorteur])

    // Méthode pour récupérer les valeurs de la victime sélectionnée après un re-render de la page
    useEffect(() => {

        axios.get('/priorites').then(response => {
            setPrioriteData(response.data)
        }).then(() => {
            axios.get('/criticites').then(response => {
                setCriticiteData(response.data)
            }).then(() => {
                axios.get('/correctPrevents').then(response => {
                    setCorrectPreventData(response.data)
                }).then(() => {
                    axios.get('/users').then(response => { setUserData(response.data) })
                }).then(() => {
                    axios.get('/users/pilotes').then(response => { setPiloteData(response.data) })
                }).then(() => {
                    if (actionId) {
                        axios.get(`/actions/${actionId}`).then(response => {
                            console.log(response.data);
                            setActionData({
                                action: response.data.action
                            })
                        })

                    }
                })
            })
        })
        console.log(actionData)
    }, [actionId, actionData]
    )

// Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(actionData);


        if (actionId) {
            axios.put(`/actions/${actionId}`, {
                ...actionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListAction')
            })
        } else {
            axios.post('/actions', {
                ...actionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListAction')
            })
        }
    }

    //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement du rapport en BDD
    const handleChange = (event) => {
        let currentState = { ...actionData };
        currentState[event.target.name] = event.target.value;
        setActionData(currentState);

        if (userInfo.role === 2) {
            setEtape(1)
            setActionData(actionData => ({ ...actionData, etape: quelleEtape }))
        }

        if (event.target.name === "delai") {
            setAutoFocusDelai(true);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "titre") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(true);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "descriptionAction") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(true);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "etat") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(true);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "pilote") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(true);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "porteur") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(true);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "prioriteid") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(true);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "criticite") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(true);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(false);
        }

        if (event.target.name === "correctPrevent") {
            setAutoFocusDelai(false);
            setAutoFocusTitre(false);
            setAutoFocusActionImmediat(false);
            setAutoFocusDescription(false);
            setAutoFocusEtat(false);
            setAutoFocusPilote(false);
            setAutoFocusPorteur(false);
            setAutoFocusPriorite(false);
            setAutoFocusCriticite(false);
            setAutoFocusPJ(false);
            setAutoFocusCorrectPrevent(true);
        }

        if (event.target.name === "checkEtape" && event.target.value === "pvRedac") {
            setPVRedacteur(true);
            setPVPilote(false);
            setPVPorteur(false);
            setPVValidateur(false);
        }

        if (event.target.name === "checkEtape" && event.target.value === "pvPilot") {
            setPVRedacteur(false);
            setPVPilote(true);
            setPVPorteur(false);
            setPVValidateur(false);
        }

        if (event.target.name === "checkEtape" && event.target.value === "pvPort") {
            setPVRedacteur(false);
            setPVPilote(false);
            setPVPorteur(true);
            setPVValidateur(false);
        }

        if (event.target.name === "checkEtape" && event.target.value === "pvValid") {
            setPVRedacteur(false);
            setPVPilote(false);
            setPVPorteur(false);
            setPVValidateur(true);
        }
    }

    //Méthodes pour gérer les changements de valeurs dans certains champs, le titre de la méthode indique le champ concerné.
    const handlePiloteChange = (value) => {
        setPilote(value.value);
    }

    const handlePorteurChange = (value) => {
        setPorteur(value.value);
    }

    //Constantes pour afficher les champs visibles uniquement par les admin
    const InputDelai = () =>
        <div>
            <label className="">Délai de résolution</label>
            <input type="date" name="delai" id="delai" defaultValue={setInitialDate(today)} value={actionData.delai}
                onChange={handleChange} className="form-control" autoFocus={autoFocusDelai} />
        </div>;

    const ModEtape = () => <div>
        <label>Avancement de l'action</label>
        <select className="form-control" name="checkEtape" value={actionData.checkEtape} onChange={handleChange}>
            <option>Selectionnez une étape</option>
            <option value="pvRedac">Etape redacteur</option>
            <option value="pvPilot">Etape pilote</option>
            <option value="pvPort">Etape porteur</option>
            <option value="pvValid">Etape validateur</option>
        </select>
    </div>;

//Constantes pour afficher les différents points de vue (PV) en fonction du rôle indiqué dans le titre de la constante
    const PvRedacteur = () => <div>
        <form onSubmit={onSubmit} className="ml-3 mr-3 mb-1">
            <div className="form-check-inline">
                <div>
                    <label className="">Délai de résolution</label>
                    <input type="date" name="delai" id="delai" defaultValue={setInitialDate(today)} value={actionData.delai}
                        autoFocus={autoFocusDelai} onChange={handleChange} className="form-control" />
                </div>
                <input type="hidden" name="is_action_immediate" value={actionData.is_action_immediate}
                    onClick={toggleDelai} className="form-check-input" />
            </div>
            <div>
                {/* <label className="">Titre</label> */}
                <input type="text" autoFocus={autoFocusTitre} hidden
                    name="titre" value={actionData.titre} onChange={handleChange} className="form-control" />
            </div>
            <label className="">Description de l'action à mener</label>
            <input type="text" className="form-control" name="descriptionAction" value={actionData.descriptionAction}
                autoFocus={autoFocusDescription} onChange={handleChange} placeholder="Description du rapport"></input>
            <input type="hidden" autoFocus={autoFocusEtat} className="form-control" name="etat" value={actionData.etat} onChange={handleChange} placeholder="Etat d'avancement"></input>
            <div className="d-flex col justify-content-around">

                <div className="col-5">
                    <label className="">Pilote</label>
                    <Select name="pilote" value={quelPilote}
                        options={
                            pilotes && pilotes.map((pilote) => ({
                                label: pilote.lastName, value: pilote.lastName,
                            }
                            ))}
                        placeholder={"Selectionnez un pilote"} autoFocus={autoFocusPilote} onChange={value => handlePiloteChange(value)} />
                </div>

                <div className="col-5">
                    <label className="">Porteur</label>
                    <Select name="porteur" options={
                        users && users.map((porteur) => ({
                            label: porteur.lastName, value: porteur.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPorteur} hidden placeholder={"Selectionnez un porteur"} onChange={value => handlePorteurChange(value)} />
                </div>

                {/* <div>
                                 <label className="">Priorité</label> 
                                <select hidden name="prioriteid" value={priorites.priorite} className="form-control"
                                 autoFocus={autoFocusPriorite} >
                                    {priorites.map(priorite => (
                                        <option value={priorite.id} key={priorite.id}>{priorite.accrPriorite}</option>
                                    ))}
                                </select>
                            </div> */}
            </div>

            <div className="d-flex col justify-content-around">
                <div>
                    {/* <label className="">Criticité</label> */}
                    <select hidden name="criticite" value={criticites.criticite} className="form-control"
                        autoFocus={autoFocusCriticite} >
                        {criticites.map(criticite => (
                            <option value={criticite.id} key={criticite.id}>{criticite.criticite}</option>
                        ))}
                    </select>
                </div>
                <div>
                    {/* <label className="">Corrective ou préventive ?</label> */}
                    <select hidden name="correctPrevent" value={correctPrevents.correctPrevent} className="form-control"
                        autoFocus={autoFocusCorrectPrevent} >
                        {correctPrevents.map(correctPrevent => (
                            <option value={correctPrevent.id} key={correctPrevent.id}>{correctPrevent.correctPrevent}</option>
                        ))}
                    </select>
                </div>


            </div>
            <label className="">Liste des pièces jointes</label>
            <div className="input-group mb-3">
                <label className="input-group-text" for="inputGroupFile01">Upload</label>
                <input autoFocus={autoFocusPJ} type="file" className="form-control" id="inputGroupFile01" />
            </div>

            <div className="d-flex justify-content-end mt-3">
                <div className="d-flex justify-content-end mt-3">
                    <button type="submit" className="btn btn-sm btn-primary" value="Enregistrer"><FontAwesomeIcon icon={faSave} /></button>
                </div>
            </div>
        </form>

    </div>;

    const PvPilote = () => <div>
        <form onSubmit={onSubmit} className="ml-3 mr-3 mb-1">
            <div className="form-check-inline">
                <label className="form-check-label">Action immédiate ?</label>
                <input type="checkbox" name="is_action_immediate" autoFocus={autoFocusActionImmediat}
                    value={actionData.is_action_immediate} onClick={toggleDelai} className="form-check-input" />
                <div>
                    <label className="">Délai de résolution</label>
                    <input type="date" name="delai" id="delai" defaultValue={setInitialDate(today)} value={actionData.delai}
                        autoFocus={autoFocusDelai} onChange={handleChange} className="form-control" />
                </div>
            </div>

            <div>
                {/* <label className="">Titre</label> */}
                <input type="text" name="titre" value={actionData.titre} onChange={handleChange}
                    hidden autoFocus={autoFocusTitre} className="form-control" />
            </div>
            <label className="">Description de l'action à mener</label>
            <textarea className="form-control" name="descriptionAction" autoFocus={autoFocusDescription}
                value={actionData.descriptionAction} onChange={handleChange} placeholder="Description du rapport"></textarea>
            <label className="">Etat d'avancement</label>
            <textarea type="hidden" className="form-control" autoFocus={autoFocusEtat}
                name="etat" value={actionData.etat} onChange={handleChange} placeholder="Etat d'avancement"></textarea>
            <div className="d-flex col justify-content-around">

                <div>
                    <label>Pilote</label>
                    <Select name="pilote" options={
                        pilotes && pilotes.map((pilote) => ({
                            label: pilote.lastName, value: pilote.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPilote} disabled placeholder={"Selectionnez un pilote"} onChange={value => handlePiloteChange(value)} />
                </div>

                <div>
                    <label className="">Porteur</label>
                    <Select name="porteur" options={
                        users && users.map((porteur) => ({
                            label: porteur.lastName, value: porteur.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPorteur} hidden placeholder={"Selectionnez un porteur"} onChange={value => handlePorteurChange(value)} />
                </div>

                <div>
                    {/* <label className="">Priorité</label> */}
                    <select hidden name="prioriteid" value={priorites.priorite}
                        autoFocus={autoFocusPriorite} className="form-control">
                        {priorites.map(priorite => (
                            <option value={priorite.id} key={priorite.id}>{priorite.accrPriorite}</option>
                        ))}
                    </select>
                </div>
            </div>

            <div className="d-flex col justify-content-around">
                <div>
                    {/* <label className="">Criticité</label> */}
                    <select hidden name="criticite" value={criticites.criticite}
                        autoFocus={autoFocusCriticite} className="form-control">
                        {criticites.map(criticite => (
                            <option value={criticite.id} key={criticite.id}>{criticite.criticite}</option>
                        ))}
                    </select>
                </div>
                <div>
                    {/* <label className="">Corrective ou préventive ?</label> */}
                    <select hidden name="correctPrevent" value={correctPrevents.correctPrevent}
                        autoFocus={autoFocusCorrectPrevent} className="form-control">
                        {correctPrevents.map(correctPrevent => (
                            <option value={correctPrevent.id} key={correctPrevent.id}>{correctPrevent.correctPrevent}</option>
                        ))}
                    </select>
                </div>

                {showDelai ? <InputDelai /> : null}

            </div>
            <label className="">Liste des pièces jointes</label>
            <div className="input-group mb-3">
                <label className="input-group-text" for="inputGroupFile01">Upload</label>
                <input autoFocus={autoFocusPJ} type="file" className="form-control" id="inputGroupFile01" />
            </div>

            <div className="d-flex justify-content-end mt-3">
                <div className="d-flex justify-content-end mt-3">
                    <button type="submit" className="btn btn-sm btn-primary" value="Enregistrer"><FontAwesomeIcon icon={faSave} /></button>
                </div>
            </div>
        </form>

    </div>;

    const PvPorteur = () => <div>
        <form onSubmit={onSubmit} className="ml-3 mr-3 mb-1">
            <div className="form-check-inline">
                <div>
                    <label className="">Délai de résolution</label>
                    <input type="date" name="delai" id="delai" defaultValue={setInitialDate(today)} value={actionData.delai}
                        autoFocus={autoFocusDelai} onChange={handleChange} className="form-control" />
                </div>
                <input type="hidden" name="is_action_immediate" autoFocus={autoFocusActionImmediat}
                    value={actionData.is_action_immediate} onClick={toggleDelai} className="form-check-input" />
            </div>
            <div>
                {/* <label className="">Titre</label> */}
                <input disabled type="text" name="titre" value={actionData.titre} onChange={handleChange}
                    autoFocus={autoFocusTitre} className="form-control" hidden />
            </div>
            <label className="">Description de l'action à mener</label>
            <textarea className="form-control" name="descriptionAction" autoFocus={autoFocusDescription}
                value={actionData.descriptionAction} onChange={handleChange} placeholder="Description du rapport"></textarea>
            <label className="">Etat d'avancement</label>
            <textarea className="form-control" name="etat" value={actionData.etat} autoFocus={autoFocusEtat}
                onChange={handleChange} placeholder="Etat d'avancement"></textarea>
            <div className="d-flex col justify-content-around">

                <div>
                    <label className="">Pilote</label>
                    <Select name="pilote" options={
                        pilotes && pilotes.map((pilote) => ({
                            label: pilote.lastName, value: pilote.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPilote} disabled placeholder={"Selectionnez un pilote"} onChange={value => handlePiloteChange(value)} />
                </div>

                <div>
                    <label className="">Porteur</label>
                    <Select name="porteur" options={
                        users && users.map((porteur) => ({
                            label: porteur.lastName, value: porteur.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPorteur} hidden hidden placeholder={"Selectionnez un porteur"} onChange={value => handlePorteurChange(value)} />
                </div>

                <div>
                    {/* <label className="">Priorité</label> */}
                    <select hidden disabled name="prioriteid" value={priorites.priorite}
                        autoFocus={autoFocusPriorite} className="form-control">
                        {priorites.map(priorite => (
                            <option value={priorite.id} key={priorite.id}>{priorite.accrPriorite}</option>
                        ))}
                    </select>
                </div>
            </div>

            <div className="d-flex col justify-content-around">
                <div>
                    {/* <label className="">Criticité</label> */}
                    <select hidden disabled name="criticite" value={criticites.criticite}
                        autoFocus={autoFocusCriticite} className="form-control">
                        {criticites.map(criticite => (
                            <option value={criticite.id} key={criticite.id}>{criticite.criticite}</option>
                        ))}
                    </select>
                </div>
                <div>
                    {/* <label className="">Corrective ou préventive ?</label> */}
                    <select hidden disabled name="correctPrevent" value={correctPrevents.correctPrevent}
                        autoFocus={autoFocusCorrectPrevent} className="form-control">
                        {correctPrevents.map(correctPrevent => (
                            <option value={correctPrevent.id} key={correctPrevent.id}>{correctPrevent.correctPrevent}</option>
                        ))}
                    </select>
                </div>



            </div>
            <label className="">Liste des pièces jointes</label>
            <div className="input-group mb-3">
                <label className="input-group-text" for="inputGroupFile01">Upload</label>
                <input autoFocus={autoFocusPJ} type="file" className="form-control" id="inputGroupFile01" />
            </div>


            <div className="d-flex justify-content-end mt-3">
                <div className="d-flex justify-content-end mt-3">
                    <button type="submit" className="btn btn-sm btn-primary" value="Enregistrer"><FontAwesomeIcon icon={faSave} /></button>
                </div>
            </div>
        </form>

    </div>;

    const PvValidateur = () => <div>
        <form onSubmit={onSubmit} className="ml-3 mr-3 mb-1">
            <div className="form-check-inline">
                <div>
                    <label className="">Délai de résolution</label>
                    <input type="date" name="delai" id="delai" defaultValue={setInitialDate(today)} value={actionData.delai}
                        autoFocus={autoFocusDelai} onChange={handleChange} className="form-control" />
                </div>
                <input type="hidden" name="is_action_immediate" value={actionData.is_action_immediate}
                    autoFocus={autoFocusActionImmediat} onClick={toggleDelai} className="form-check-input" />
            </div>
            <div>
                {/* <label className="">Titre</label> */}
                <input type="text" autoFocus={autoFocusTitre} hidden
                    name="titre" value={actionData.titre} onChange={handleChange} className="form-control" />
            </div>
            <label className="">Description de l'action à mener</label>
            <textarea className="form-control" name="descriptionAction" autoFocus={autoFocusDescription}
                value={actionData.descriptionAction} onChange={handleChange} placeholder="Description du rapport"></textarea>
            <label className="">Etat d'avancement</label>
            <textarea disabled className="form-control" name="etat" autoFocus={autoFocusEtat}
                value={actionData.etat} onChange={handleChange} placeholder="Etat d'avancement"></textarea>
            <div className="d-flex col justify-content-around">

                <div>
                    <label className="">Pilote</label>
                    <Select name="pilote" options={
                        pilotes && pilotes.map((pilote) => ({
                            label: pilote.lastName, value: pilote.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPilote} placeholder={"Selectionnez un pilote"} onChange={value => handlePiloteChange(value)} />
                </div>

                <div>
                    <label className="">Porteur</label>
                    <Select name="porteur" options={
                        users && users.map((porteur) => ({
                            label: porteur.lastName, value: porteur.lastName,
                        }
                        ))}
                        autoFocus={autoFocusPorteur} hidden placeholder={"Selectionnez un porteur"} onChange={value => handlePorteurChange(value)} />
                </div>

                <div>
                    {/* <label className="">Priorité</label> */}
                    <select hidden name="prioriteid" value={priorites.priorite} className="form-control">
                        {priorites.map(priorite => (
                            <option value={priorite.id} key={priorite.id}>{priorite.accrPriorite}</option>
                        ))}
                    </select>
                </div>
            </div>

            <div className="d-flex col justify-content-around">
                <div>
                    {/* <label className="">Criticité</label> */}
                    <select hidden name="criticite" value={criticites.criticite} className="form-control">
                        {criticites.map(criticite => (
                            <option value={criticite.id} key={criticite.id}>{criticite.criticite}</option>
                        ))}
                    </select>
                </div>
                <div>
                    {/* <label className="">Corrective ou préventive ?</label> */}
                    <select hidden name="correctPrevent" value={correctPrevents.correctPrevent}
                        autoFocus={autoFocusCorrectPrevent} className="form-control">
                        {correctPrevents.map(correctPrevent => (
                            <option value={correctPrevent.id} key={correctPrevent.id}>{correctPrevent.correctPrevent}</option>
                        ))}
                    </select>
                </div>


            </div>
            <label className="">Liste des pièces jointes</label>
            <div className="input-group mb-3">
                <label className="input-group-text" for="inputGroupFile01">Upload</label>
                <input type="file" className="form-control" id="inputGroupFile01" autoFocus={autoFocusPJ} />
            </div>

            <div className="d-flex justify-content-end mt-3">
                <div className="d-flex justify-content-end mt-3">
                    <button type="submit" name="etape" className="btn btn-sm btn-primary" value="Enregistrer"><FontAwesomeIcon icon={faSave} /></button>
                </div>
            </div>
        </form>

    </div>;

 //méthode pour afficher le formulaire à l'écran
    return (
        <div className="d-flex justify-content-center mt-1">
            <div className="col-8">
                <div className="card bg-light">
                    <h2 className="bg-info fw-bold text-center">Ajouter une action</h2>
                    {showModEtape ? <ModEtape /> : null}
                    {showPVRedacteur ? <PvRedacteur /> : null}
                    {showPVPilote ? <PvPilote /> : null}
                    {showPVPorteur ? <PvPorteur /> : null}
                    {showPVValidateur ? <PvValidateur /> : null}
                </div>
            </div>
        </div>
    )
}