import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des étapes de validation
export default function AddEtapeValidation() {

//Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { etapeValidationId } = useParams();
    const [etapeValidationData, setEtapeValidationData] = useState({ etapeValidation: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

//Méthode pour vérifier que l'étape de validation n'existe pas déjà
    useEffect(() => {
        if (etapeValidationId) {
            axios.get(`/etapeValidations/${etapeValidationId}`).then(response => {
                console.log(response.data);
                setEtapeValidationData({
                    etapeValidation: response.data.etapeValidation,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [etapeValidationId])

    //Méthode pour l'enregistrement de l'étape de validation
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(etapeValidationData);
        if (etapeValidationId) {
            axios.put(`/etapeValidations/${etapeValidationId}`, {
                ...etapeValidationData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListEtapeValidation')
            })
        } else {
            axios.post('/etapeValidations', {
                ...etapeValidationData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListEtapeValidation')
            })
        }
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...etapeValidationData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setEtapeValidationData(currentState);
    }

     //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...etapeValidationData };
        currentState[event.target.name] = event.target.value;
        setEtapeValidationData(currentState);
    }

     //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une etapeValidation</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">EtapeValidation</label>
                        <input type="text" name="etapeValidation" value={etapeValidationData.etapeValidation} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={etapeValidationData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

