import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des types
export default function ListType() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [types, setTypeData] = React.useState([]);

    const fetchType = () => {
        axios.get('/types').then(response => {
            setTypeData(response.data)
            console.log(response.data);
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        axios.get('/types').then(response => {
            setTypeData(response.data)
            console.log(response.data);
            console.log("******************* LIST COMPLETE*******************")
            console.log(response.data)
            console.log("******************* REPARTITION ************************")
            console.log(response.data[2].repartition.repartition)
        })
        console.log("******************* USERS ************************")
        console.log(types.map(type => (
            type.repartition.repartition
        )))
    }, [])

    //Constante pour afficher les entrées de la liste après chaque re-render
    const handleDelete = (typeId) => {
        axios.delete(`/types/${typeId}`).then(response => {
            fetchType();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Types</h4>
        {types.length === 0 ? "Pas de type disponible" : null}
        <Link to="/AddType" className="btn btn-outline-info btn-sm m-3">Ajouter une type</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>Accronyme</th>
                        <th>Origine</th>
                        <th>repartition</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                      {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {types.map(type => (
                        <tr key={type.id}>
                            <td>{type.id}</td>
                            <td>{type.origine}</td>
                            <td>{type.accOrigine}</td>
                            {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{type.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td>{type.repartition.repartition}</td>
                            <td><Link to={`/AddType/${type.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(type.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

