
import axios from "axios";
import React, { useEffect, useState } from "react";
import { G2, Chart, Geom, Axis, Tooltip, Coord, Label, Legend, View, Guide, Shape, Facet, Util, getTheme } from 'bizcharts';
import DataSet from '@antv/data-set';
import Pyramide from '../../assets/xls/Pyramide.xls'
import { useHistory, useParams, Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFileExcel } from "@fortawesome/free-solid-svg-icons";

// Classe pour afficher les statistiques d'accidentologie
export default function StatsAccidentologie() {

    // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    let { typeId } = useParams();
    const [typeData, setTypeData] = useState({});
    const [departements, setdepartementData] = useState([]);
    const [repartitions, setRepartitionData] = useState([]);
    const [correctPrevents, setCorrectPreventData] = React.useState([]);
    const history = useHistory();

    //Constantes avec des stats notées en dur pour donner un exemple visuel
    const newdata = [
        {
            time: 'Stat',
            level_s_amount: 123,
            level_a_amount: 223,
            level_b_amount: 310,
            level_c_amount: 412,
            level_other_amount: 312,
        },
    ];

 // Méthode pour récupérer les valeurs de la victime sélectionnée après un re-render de la page
    useEffect(() => {
        setdepartementData([
            axios.get('/departements').then(response => {
                setdepartementData(response.data)
            })
        ])
        setCorrectPreventData([
            axios.get('/correctPrevents').then(response => {
                setCorrectPreventData(response.data)
            })
        ])
        axios.get('/repartitions/').then(response => {
            setRepartitionData(response.data)
            setTypeData({
                origine: '',
                accOrigine: '',
                repartitionId: response.data[0].id,
            })

        }).then(() => {
            if (typeId) {
                axios.get(`/types/${typeId}`).then(response => {
                    console.log(response.data);
                    setTypeData({
                        origine: response.data.origine,
                        accOrigine: response.data.accOrigine,
                        repartitionId: response.data.repartition.id,
                        isAffiche: response.data.isAffiche,
                    })
                })
            }
        })
    }, [typeId])

    // Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(typeData);
        if (typeId) {
            axios.put(`/types/${typeId}`, {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        } else {
            axios.post('/types', {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        }
    }

     //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement du rapport en BDD
    const handleChange = (event) => {
        let currentState = { ...typeData };
        currentState[event.target.name] = event.target.value;
        setTypeData(currentState);
    }

      //Constantes pour le formatage des statistiques
    const ds = new DataSet();
    const dv = ds.createView().source(newdata);
    dv.transform({
        type: 'fold',
        fields: [
            'level_s_amount',
            'level_a_amount',
            'level_b_amount',
            'level_c_amount',
            'level_s_gaap', 'level_a_gaap', 'level_b_gaap', 'level_c_gaap'
        ],
        key: 'key',
        value: 'value',
    })
        .transform({
            type: 'map',
            callback: (obj) => {
                if (obj.key.indexOf('amount') !== -1) {
                    obj.type = 'PQA'
                } else if (obj.key.indexOf('gaap') !== -1) {
                    obj.type = 'GAAP收入'
                }
                obj.level = obj.key.split('_')[1].toUpperCase() + '级'
                // console.log(obj)
                return obj;
            },
        });

         //méthode pour afficher les statistiques à l'écran
    return (
        <div className="Container card">

            <h3 className="text-center">Statistique sur Accidentologie</h3>
            <div className="d-flex justify-content-sm-center ">
                <div className="m-2">
                    <label className="">Année</label>
                    <select name="departementId" value={departements.departementId} onChange={handleChange} className="form-control" >
                        <option value="">Tout</option>
                        <option value="">2019</option>
                        <option value="">2020</option>
                        <option value="">2021</option>
                        <option value="">2022</option>

                    </select>
                </div>
                <div className="m-2">
                    <label className="">Département</label>
                    <select name="departementId" value={departements.departementId} onChange={handleChange} className="form-control" >
                        <option value=""  >Tout</option>
                        {
                            departements.map(departement => (
                                <option value={departement.id} key={departement.id} >{departement.departement}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">pilote</label>
                    <select name="departementId" value={departements.departementId} onChange={handleChange} className="form-control" >
                        <option value=""  >Tout</option>
                        {
                            departements.map(departement => (
                                <option value={departement.id} key={departement.id} >{departement.departement}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">repartitions</label>
                    <select name="repartitionsId" value={repartitions.repartitionsId} onChange={handleChange} className="form-control" >
                        <option value="">Tout</option>
                        {
                            repartitions.map(repartition => (
                                <option value={repartition.id} key={repartition.id} >{repartition.repartition}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">Capa</label>
                    <select name="correctPreventId" value={correctPrevents.correctPreventId} onChange={handleChange} className="form-control" >
                        <option value="">Tout</option>
                        {
                            correctPrevents.map(correctPrevent => (
                                <option value={correctPrevent.id} key={correctPrevent.id} >{correctPrevent.correctPrevent}</option>
                            ))}
                    </select>
                </div>
            </div>
            <div className="col-12 mt-3">
                <Link to={Pyramide} className="btn btn-success" target="_blank" >
                    <FontAwesomeIcon icon={faFileExcel} /> Pyramide
                </Link>
                <Chart height={400} onClick={e => {
                    debugger
                }} data={dv.rows} autoFit>
                    <Tooltip>
                        {
                            (time, items, ...args) => {
                                if (items?.length) {
                                    const { data: { type } } = items[0];
                                    const legendItems = args[2].view.getController('legend').components[0].component.getItems()
                                    const legendMap = legendItems.reduce((pre, cur) => {
                                        const { value, marker } = cur;
                                        pre[value] = marker
                                        return pre
                                    }, {})
                                    const tooltipItems = dv.rows.filter(item => {
                                        return item.time === time && item.type === type
                                    })
                                    // 这里可以写react组件
                                    return <div>
                                        <h5 style={{ marginBottom: -8 }}><b>{time}</b></h5>
                                        <ul>
                                            {
                                                tooltipItems.map(t => {
                                                    return <li style={{ margin: 8 }}>
                                                        <span className='g2-tooltip-marker' style={{ backgroundColor: legendMap[t.level].style.fill }} />
                                                        {t.type} {t.value}
                                                    </li>
                                                })
                                            }
                                        </ul>
                                    </div>
                                }
                            }
                        }</Tooltip>
                    <Geom
                        type="interval"
                        position="time*value"
                        color="level"
                        style={{
                            stroke: '#fff',
                            lineWidth: 1,
                        }}
                        adjust={[
                            {
                                type: 'dodge',
                                dodgeBy: 'type',
                                marginRatio: 0,
                            },
                            {
                                type: 'stack',
                            },
                        ]}
                    >
                    </Geom>
                </Chart>
            </div>
        </div>
    )
}

