import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des types d'AT
export default function ListTypeAt() {

     // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [typeAts, setTypeAtData] = React.useState([]);
    const fetchTypeAt = () => {
        axios.get('/typeAts').then(response => {
            setTypeAtData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setTypeAtData([
            axios.get('/typeAts').then(response => {
                setTypeAtData(response.data)
            })
        ])
    }, [])

    //Constante pour afficher les entrées de la liste après chaque re-render
    const handleDelete = (typeAtId) => {
        axios.delete(`/typeAts/${typeAtId}`).then(response => {
            fetchTypeAt();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des TypeAt</h4>
        {typeAts.length === 0 ? "Pas de typeAt disponible" : null}
        <Link to="/AddTypeAt" className="btn btn-outline-info btn-sm m-3">Ajouter une typeAt</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>typeAt</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {typeAts.map(typeAt => (
                        <tr key={typeAt.id}>
                            <td>{typeAt.id}</td>
                            <td>{typeAt.typeAt}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{typeAt.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddTypeAt/${typeAt.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(typeAt.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

