//Clase servant au rooting au sein de l'application


import React from "react";
import "./App.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter, Route, Redirect } from "react-router-dom";
import ListCategorie from './components/admin/categorie/ListCategorie';
import AddCategorie from './components/admin/categorie/AddCategorie';
import Login from './components/User/Login';
import AddUser from './components/User/AddUser';
import NavigationBar from "./components/NavBar/NavigationBar";
import AtChoix from "./components/Rapports/AT/ATChoix";
import AtInterne from "./components/Rapports/AT/ATInterne";
import AtExterne from "./components/Rapports/AT/ATExterne";
import Home from "./components/Home";
import ListAction from './components/Rapports/Action/ListAction';
import AddAction from './components/Rapports/Action/AddAction';
import ListContrat from './components/admin/contrat/ListContrat';
import AddContrat from './components/admin/contrat/AddContrat';
import ListCorrectPrevent from './components/admin/CorrectPrevent/ListCorrectPrevent';
import AddCorrectPrevent from './components/admin/CorrectPrevent/AddCorrectPrevent';
import ListCriticite from './components/admin/Criticite/ListCriticite';
import AddCriticite from './components/admin/Criticite/AddCriticite';
import ListCsp from './components/admin/Csp/ListCsp';
import AddCsp from './components/admin/Csp/AddCsp';
import ListDepartement from './components/admin/Departement/ListDepartement';
import AddDepartement from './components/admin/Departement/AddDepartement';
import ListEquipement from './components/admin/Equipement/ListEquipement';
import AddEquipement from './components/admin/Equipement/AddEquipement';
import ListEtapeValidation from './components/admin/EtapeValidation/ListEtapeValidation';
import AddEtapeValidation from './components/admin/EtapeValidation/AddEtapeValidation';
import ListFicheSuiviSante from './components/admin/FicheSuiviSante/ListFicheSuiviSante';
import AddFicheSuiviSante from './components/admin/FicheSuiviSante/AddFicheSuiviSante';
import ListListingMail from './components/admin/ListingMail/ListListingMail';
import AddListingMail from './components/admin/ListingMail/AddListingMail';
import ListNatureAccident from './components/admin/NatureAccident/ListNatureAccident';
import AddNatureAccident from './components/admin/NatureAccident/AddNatureAccident';
import ListOrigineLesions from './components/admin/OrigineLesions/ListOrigineLesions';
import AddOrigineLesions from './components/admin/OrigineLesions/AddOrigineLesions';
import ListPiecesJointes from './components/admin/PiecesJointes/ListPiecesJointes';
import AddPiecesJointes from './components/admin/PiecesJointes/AddPiecesJointes';
import ListPriorite from './components/admin/Priorite/ListPriorite';
import AddPriorite from './components/admin/Priorite/AddPriorite';
import ListRepartition from './components/admin/Repartition/ListRepartition';
import AddRepartition from './components/admin/Repartition/AddRepartition';
import ListSexe from './components/admin/Sexe/ListSexe';
import AddSexe from './components/admin/Sexe/AddSexe';
import ListSiegeLesions from './components/admin/SiegeLesions/ListSiegeLesions';
import AddSiegeLesions from './components/admin/SiegeLesions/AddSiegeLesions';
import ListSite from './components/admin/Site/ListSite';
import AddSite from './components/admin/Site/AddSite';
import ListStatut from './components/admin/Statut/ListStatut';
import AddStatut from './components/admin/Statut/AddStatut';
import ListType from './components/admin/Type/ListType';
import AddType from './components/admin/Type/AddType';
import ListTypeAt from './components/admin/TypeAt/ListTypeAt';
import AddTypeAt from './components/admin/TypeAt/AddTypeAt';
import ListTypeAction from './components/admin/TypeAction/ListTypeAction';
import AddTypeAction from './components/admin/TypeAction/AddTypeAction';
import ListTypeRapport from './components/admin/TypeRapport/ListTypeRapport';
import AddTypeRapport from './components/admin/TypeRapport/AddTypeRapport';
import AddPqa from './components/Rapports/PQA/AddPqa';
import AddAutreRapports from './components/Rapports/AutreRapport/AddAutreRapports';
import AddAutreRapportsEdit from './components/Rapports/AutreRapport/AddAutreRapportsEdit';
import ListRapport from "./components/Rapports/ListRapport/ListRapport";
import AdminUserList from "./components/admin/user/AdminUserList";
import AdminAddUser from "./components/admin/user/AdminAddUser";
import ListActionFiltrer from './components/Rapports/Action/ListActionFiltrer';
import HomeStat from './components/Statistiques/HomeStat';
import 'react-app-polyfill/stable';
import StatsSurAnnee from './components/Statistiques/StatsSurAnnee';
import Stats12MoisGlissant from './components/Statistiques/Stats12MoisGlissant';
import StatsAccidentologie from './components/Statistiques/StatsAccidentologie';
import ATInterneEdit from './components/Rapports/AT/ATInterneEdit';
import ATExterneEdit from './components/Rapports/AT/ATExterneEdit';
import StatsTFTG from './components/Statistiques/StatsTFTG';
import StatsRepartition from './components/Statistiques/StatsRepartition';
import AddPQAEdit from './components/Rapports/PQA/AddPqaEdit';

function App() {

  const [userInfo, setUserInfo] = React.useState('');

  return (
    <BrowserRouter>
      {!userInfo && <Redirect to="/Login" />}
      {userInfo && <NavigationBar userInfo={userInfo} setUserInfo={setUserInfo} />}
      <Route path="/home"><Home /></Route>
      <Route exact path="/AtChoix"><AtChoix /></Route>
      <Route exact path="/AtInterne"><AtInterne /></Route>
      <Route exact path="/AtExterne"><AtExterne /></Route>
      <Route path="/list"></Route>
      <Route path="/AddPqa"><AddPqa /></Route>
      <Route path="/ListCategorie"><ListCategorie /></Route>
      <Route exact path="/AddCategorie"><AddCategorie /></Route>
      <Route exact path="/AddCategorie/:categorieId"><AddCategorie /></Route>
      <Route path="/Login"><Login setUserInfo={setUserInfo} /></Route>
      <Route path="/AddUser"><AddUser setUserInfo={setUserInfo} /></Route>
      <Route path="/ListAction"><ListAction /></Route>
      <Route path="/ListActionFiltrer"><ListActionFiltrer /></Route>
      <Route exact path="/AddAction"><AddAction userInfo={userInfo} setUserInfo={setUserInfo} /></Route>
      <Route exact path="/AddAction/:actionId"><AddAction userInfo={userInfo} setUserInfo={setUserInfo} /></Route>
      <Route path="/ListContrat"><ListContrat /></Route>
      <Route exact path="/AddContrat"><AddContrat /></Route>
      <Route exact path="/AddContrat/:contratId"><AddContrat /></Route>
      <Route path="/ListCorrectPrevent"><ListCorrectPrevent /></Route>
      <Route exact path="/AddCorrectPrevent"><AddCorrectPrevent /></Route>
      <Route exact path="/AddCorrectPrevent/:correctPreventId"><AddCorrectPrevent /></Route>
      <Route path="/ListCriticite"><ListCriticite /></Route>
      <Route exact path="/AddCriticite"><AddCriticite /></Route>
      <Route exact path="/AddCriticite/:criticiteId"><AddCriticite /></Route>
      <Route path="/ListCsp"><ListCsp /></Route>
      <Route exact path="/AddCsp"><AddCsp /></Route>
      <Route exact path="/AddCsp/:cspId"><AddCsp /></Route>
      <Route path="/ListDepartement"><ListDepartement /></Route>
      <Route exact path="/AddDepartement"><AddDepartement /></Route>
      <Route exact path="/AddDepartement/:departementId"><AddDepartement /></Route>
      <Route path="/ListEquipement"><ListEquipement /></Route>
      <Route exact path="/AddEquipement"><AddEquipement /></Route>
      <Route exact path="/AddEquipement/:EquipementId"><AddEquipement /></Route>
      <Route path="/ListEtapeValidation"><ListEtapeValidation /></Route>
      <Route exact path="/AddEtapeValidation"><AddEtapeValidation /></Route>
      <Route exact path="/AddEtapeValidation/:etapeValidationId"><AddEtapeValidation /></Route>
      <Route path="/ListFicheSuiviSante"><ListFicheSuiviSante /></Route>
      <Route exact path="/AddFicheSuiviSante"><AddFicheSuiviSante /></Route>
      <Route exact path="/AddFicheSuiviSante/:ficheSuiviSanteId"><AddFicheSuiviSante /></Route>
      <Route path="/ListListingMail"><ListListingMail /></Route>
      <Route exact path="/AddListingMail"><AddListingMail /></Route>
      <Route exact path="/AddListingMail/:listingMailId"><AddListingMail /></Route>
      <Route path="/ListNatureAccident"><ListNatureAccident /></Route>
      <Route exact path="/AddNatureAccident"><AddNatureAccident /></Route>
      <Route exact path="/AddNatureAccident/:natureAccidentId"><AddNatureAccident /></Route>
      <Route path="/ListOrigineLesions"><ListOrigineLesions /></Route>
      <Route exact path="/AddOrigineLesions"><AddOrigineLesions /></Route>
      <Route exact path="/AddOrigineLesions/:origineLesionsId"><AddOrigineLesions /></Route>
      <Route path="/ListPiecesJointes"><ListPiecesJointes /></Route>
      <Route exact path="/AddPiecesJointes"><AddPiecesJointes /></Route>
      <Route exact path="/AddPiecesJointes/:piecesJointesId"><AddPiecesJointes /></Route>
      <Route path="/ListPriorite"><ListPriorite /></Route>
      <Route exact path="/AddPriorite"><AddPriorite /></Route>
      <Route exact path="/AddPriorite/:prioriteId"><AddPriorite /></Route>
      <Route path="/ListRepartition"><ListRepartition /></Route>
      <Route exact path="/AddRepartition"><AddRepartition /></Route>
      <Route exact path="/AddRepartition/:repartitionId"><AddRepartition /></Route>
      <Route path="/ListSexe"><ListSexe /></Route>
      <Route exact path="/AddSexe"><AddSexe /></Route>
      <Route exact path="/AddSexe/:sexeId"><AddSexe /></Route>
      <Route path="/ListSiegeLesions"><ListSiegeLesions /></Route>
      <Route exact path="/AddSiegeLesions"><AddSiegeLesions /></Route>
      <Route exact path="/AddSiegeLesions/:siegeLesionsId"><AddSiegeLesions /></Route>
      <Route path="/ListSite"><ListSite /></Route>
      <Route exact path="/AddSite"><AddSite /></Route>
      <Route exact path="/AddSite/:siteId"><AddSite /></Route>
      <Route path="/ListStatut"><ListStatut /></Route>
      <Route exact path="/AddStatut"><AddStatut /></Route>
      <Route exact path="/AddStatut/:statutId"><AddStatut /></Route>
      <Route path="/ListType"><ListType /></Route>
      <Route exact path="/AddType"><AddType /></Route>
      <Route exact path="/AddType/:typeId"><AddType /></Route>
      <Route path="/ListTypeAt"><ListTypeAt /></Route>
      <Route exact path="/AddTypeAt"><AddTypeAt /></Route>
      <Route exact path="/AddTypeAt/:typeAtId"><AddTypeAt /></Route>
      <Route path="/ListTypeAction"><ListTypeAction /></Route>
      <Route exact path="/AddTypeAction"><AddTypeAction /></Route>
      <Route exact path="/AddTypeAction/:typeActionId"><AddTypeAction /></Route>
      <Route path="/ListTypeRapport"><ListTypeRapport /></Route>
      <Route exact path="/AddTypeRapport"><AddTypeRapport /></Route>
      <Route exact path="/AddTypeRapport/:typeRapportId"><AddTypeRapport /></Route>
      <Route exact path="/AddAutreRapports"><AddAutreRapports /></Route>
      <Route exact path="/AddAutreRapportsEdit"><AddAutreRapportsEdit /></Route>
      <Route path="/ListRapport"><ListRapport /></Route>
      <Route path="/HomeStat"><HomeStat /></Route>
      <Route path="/AdminUserList"><AdminUserList /></Route>
      <Route path="/AdminAddUser"><AdminAddUser /></Route>
      <Route path="/Stats12MoisGlissant"><Stats12MoisGlissant /></Route>
      <Route path="/StatsSurAnnee"><StatsSurAnnee /></Route>
      <Route path="/StatsAccidentologie"><StatsAccidentologie /></Route>
      <Route path="/ATInterneEdit"><ATInterneEdit /></Route>
      <Route path="/ATExterneEdit"><ATExterneEdit /></Route>
      <Route path="/StatsTFTG"><StatsTFTG /></Route>
      <Route path="/StatsRepartition"><StatsRepartition /></Route>
      <Route path="/AddPQAEdit"><AddPQAEdit /></Route>
    </BrowserRouter>
  );
};
export default App;
