import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des Equipements
export default function ListEquipement() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [equipements, setEquipementData] = React.useState([]);
    const fetchEquipement = () => {
        axios.get('/equipements').then(response => {
            setEquipementData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setEquipementData([
            axios.get('/equipements').then(response => {
                setEquipementData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (equipementId) => {
        axios.delete(`/equipements/${equipementId}`).then(response => {
            fetchEquipement();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Equipement</h4>
        {equipements.length === 0 ? "Pas de equipement disponible" : null}
        <Link to="/AddEquipement" className="btn btn-outline-info btn-sm m-3">Ajouter une equipement</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>equipement</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {equipements.map(equipement => (
                        <tr key={equipement.id}>
                            <td>{equipement.id}</td>
                            <td>{equipement.equipement}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{equipement.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddEquipement/${equipement.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(equipement.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

