import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des priorités
export default function AddPriorite() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { prioriteId } = useParams();
    const [prioriteData, setPrioriteData] = useState({ priorite: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que la priorité n'existe pas déjà
    useEffect(() => {
        if (prioriteId) {
            axios.get(`/priorites/${prioriteId}`).then(response => {
                console.log(response.data);
                setPrioriteData({
                    priorite: response.data.priorite,
                    accrPriorite: response.data.accrPriorite,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [prioriteId])

    //Méthode pour l'enregistrement de la priorité
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(prioriteData);
        if (prioriteId) {
            axios.put(`/priorites/${prioriteId}`, {
                ...prioriteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListPriorite')
            })
        } else {
            axios.post('/priorites', {
                ...prioriteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListPriorite')
            })
        }
    }

       //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...prioriteData };
        currentState[event.target.name] = event.target.value;
        setPrioriteData(currentState);
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...prioriteData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setPrioriteData(currentState);
    }

     //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une priorite</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Accronyme</label>
                        <input type="text" name="priorite" value={prioriteData.priorite} onChange={handleChange} className="form-control" />
                        <label className="">Priorite</label>
                        <input type="text" name="accrPriorite" value={prioriteData.accrPriorite} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={prioriteData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

