import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des origines des lésions
export default function AddOrigineLesions() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { origineLesionsId } = useParams();
    const [origineLesionsData, setOrigineLesionsData] = React.useState({ origineLesions: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que l'origine de la lésion n'existe pas déjà
    useEffect(() => {
        if (origineLesionsId) {
            axios.get(`/origineLesionss/${origineLesionsId}`).then(response => {
                console.log(response.data);
                setOrigineLesionsData({
                    origineLesions: response.data.origineLesions,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [origineLesionsId])

      //Méthode pour l'enregistrement de l'origine de la lésion
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(origineLesionsData);
        if (origineLesionsId) {
            axios.put(`/origineLesionss/${origineLesionsId}`, {
                ...origineLesionsData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListOrigineLesions')
            })
        } else {
            axios.post('/origineLesionss', {
                ...origineLesionsData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListOrigineLesions')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...origineLesionsData };
        currentState[event.target.name] = event.target.value;
        setOrigineLesionsData(currentState);
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...origineLesionsData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setOrigineLesionsData(currentState);
    }

      //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une origineLesions</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">OrigineLesions</label>
                        <input type="text" name="origineLesions" value={origineLesionsData.origineLesions} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={origineLesionsData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

