import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des listings mail
export default function ListListingMail() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [listingMails, setListingMailData] = React.useState([]);
    const fetchListingMail = () => {
        axios.get('/listingMails').then(response => {
            setListingMailData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setListingMailData([
            axios.get('/listingMails').then(response => {
                setListingMailData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (listingMailId) => {
        axios.delete(`/listingMails/${listingMailId}`).then(response => {
            fetchListingMail();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des ListingMail</h4>
        {listingMails.length === 0 ? "Pas de listingMail disponible" : null}
        <Link to="/AddListingMail" className="btn btn-outline-info btn-sm m-3">Ajouter une listingMail</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>listingMail</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                     {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {listingMails.map(listingMail => (
                        <tr key={listingMail.id}>
                            <td>{listingMail.id}</td>
                            <td>{listingMail.listingMail}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{listingMail.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddListingMail/${listingMail.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(listingMail.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

