import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste du site
export default function AddSite() {

       //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { siteId } = useParams();
    const [siteData, setSiteData] = React.useState({ site: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

  
       //Méthode pour vérifier que le site n'existe pas déjà
    useEffect(() => {
        if (siteId) {
            axios.get(`/sites/${siteId}`).then(response => {
                console.log(response.data);
                setSiteData({
                    site: response.data.site,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [siteId])

    //Méthode pour l'enregistrement du site
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(siteData);
        if (siteId) {
            axios.put(`/sites/${siteId}`, {
                ...siteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSite')
            })
        } else {
            axios.post('/sites', {
                ...siteData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSite')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...siteData };
        currentState[event.target.name] = event.target.value;
        setSiteData(currentState);
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...siteData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setSiteData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une site</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Site</label>
                        <input type="text" name="site" value={siteData.site} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={siteData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

