import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe servant à ajouter un utilisateur manuellement
export default function AddUser() {

     // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    let { userId } = useParams();
    const [Data, setData] = useState([]);
    const [statutData, setstatutData] = useState([]);
    const [sexeData, setsexeData] = useState([]);
    const [manangerData, setmanangerData] = useState([]);
    const [siteData, setsiteData] = useState([]);
    const [cspData, setcspData] = useState([]);
    const [roleData, setroleData] = useState([]);
    const [checkboxData, setcheckboxData] = useState(false);

    const history = useHistory();

    //Méthode pour récupérer les différentes données de la BDD via axios
    useEffect(() => {
        axios.get('/statuts').then(response => {
            setstatutData(response.data)
            setData({
                statutId: response.data[0].id
            })
        }).then(() => {
            axios.get('/sexes').then(response => {
                setsexeData(response.data)
                setData({
                    sexeId: response.data[0].id
                })
            }).then(() => {
                axios.get('/users').then(response => {
                    setmanangerData(response.data)
                    setData({
                        manangerId: response.data[0].id
                    })
                }).then(() => {
                    axios.get('/sites').then(response => {
                        setsiteData(response.data)
                        setData({
                            siteid: response.data[0].id
                        })
                    }).then(() => {
                        axios.get('/csps').then(response => {
                            setcspData(response.data)
                            setData({
                                cspId: response.data[0].id
                            })
                        }).then(() => {
                            axios.get('/roles').then(response => {
                                setroleData(response.data)
                                console.log(response.data)
                                setData({
                                    roleId: response.data[0].id
                                })
                            }).then(() => {
                                if (userId) {
                                    axios.get(`/users/${userId}`).then(response => {
                                        console.log(response.data);
                                        setData({
                                            firstName: response.data.firstName,
                                            password: response.data.password,
                                            lastName: response.data.lastName,
                                            email: response.data.email,
                                            enabled: response.data.enabled,
                                            matricule: response.data.matricule,
                                            dateDeNaissance: response.data.dateDeNaissance,
                                            statutId: response.data.statut.id,
                                            sexeId: response.data.sexe.id,
                                            manangerId: response.data.mananger.id,
                                            siteId: response.data.site.id,
                                            cspId: response.data.csp.id,
                                            roleId: response.data.role.id,
                                            isAffiche: response.data.isAffiche,
                                        }
                                        )
                                    })
                                }
                            })
                        })
                    })
                })
            })
        })
    }, [userId])

    // Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(Data);
        if (userId) {
            axios.put(`/users/${userId}`, {
                ...Data
            }).then(() => {
                // rediriger vers la liste
                history.push('/AdminUserList')
            })
        } else {
            axios.post('/users', {
                ...Data
            }).then(() => {
                // rediriger vers la liste
                history.push('/AdminUserList')
            })
        }
    }

    //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement de l'utilisateur en BDD
    const handleChange = (event) => {
        let currentState = { ...Data };
        currentState[event.target.name] = event.target.value;
        setData(currentState);
    }

// Constante pour la gestion des états cochés/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...Data };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setData(currentState);
    }

    //Return pour afficher la page à l'écran
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter un utilisateur</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-6">
                    <form onSubmit={onSubmit}>
                        <div className="d-flex justify-content-start">
                            <div className="m-3">
                                <label className="">Matricule</label>
                                <input type="text" name="matricule" value={Data.matricule} onChange={handleChange} className="form-control" />
                            </div>
                            <div className="m-3">
                                <label className="">Prénom</label>
                                <input type="text" name="firstName" value={Data.firstName} onChange={handleChange} className="form-control" />
                            </div>
                            <div className="m-3">
                                <label className="">Nom</label>
                                <input type="text" name="lastName" value={Data.lastName} onChange={handleChange} className="form-control" />
                            </div>
                        </div>
                        <div className="d-flex justify-content-start">
                            <div className="m-3">
                                <label className="">E-mail</label>
                                <input type="text" name="email" value={Data.email} onChange={handleChange} className="form-control" />
                            </div>
                            {/* <div className="m-3">
                                <label className="">Activé</label>
                                <input type="checkbox" name="enabled" value={Data.enabled} onChange={handleChange} className="form-control" />
                            </div> */}
                            <div className="m-3">
                                <label>Date de naissance</label>
                                <input type="date" className="form-control"
                                    name="dateDeNaissance" onChange={handleChange} value={Data.dateDeNaissance} />
                            </div>
                        </div>
                        <div className="d-flex justify-content-start">
                            <div className="m-3">
                                <label className="">Statut</label>
                                <select name="statutId" value={Data.statutId} onChange={handleChange} className="form-control" >
                                    {statutData && statutData.map(statut => (
                                        <option value={statut.id} key={statut.id} >{statut.statut}</option>
                                    ))}
                                </select>
                            </div>
                            <div className="m-3">
                                <label className="">Sexe</label>
                                <select name="sexeId" value={Data.sexeId} onChange={handleChange} className="form-control" >
                                    {sexeData && sexeData.map(sexe => (
                                        <option value={sexe.id} key={sexe.id} >{sexe.sexe}</option>
                                    ))}
                                </select>
                            </div>
                            <div className="m-3">
                                <label className="">Manager</label>
                                <select name="manangerId" value={Data.manangerId} onChange={handleChange} className="form-control" >
                                    {manangerData && manangerData.map(mananger => (
                                        <option value={mananger.id} key={mananger.id} >{mananger.firstName}{' '}{mananger.lastName}</option>
                                    ))}
                                </select>
                            </div>
                        </div>
                        <div className="d-flex justify-content-start">
                            <div className="m-3">
                                <label className="">Site</label>
                                <select name="siteId" value={Data.siteId} onChange={handleChange} className="form-control" >
                                    {siteData && siteData.map(site => (
                                        <option value={site.id} key={site.id} >{site.site}</option>
                                    ))}
                                </select>
                            </div>
                            <div className="m-3">
                                <label className="">Csp</label>
                                <select name="cspId" value={Data.cspId} onChange={handleChange} className="form-control" >
                                    {cspData && cspData.map(csp => (
                                        <option value={csp.id} key={csp.id} >{csp.csp}</option>
                                    ))}
                                </select>
                            </div>
                            <div className="m-3">
                                <label className="">Role</label>
                                <select name="roleId" value={Data.roleId} onChange={handleChange} className="form-control" >
                                    {roleData && roleData.map(role => (
                                        <option value={role.id} key={role.id} >{role.name}</option>
                                    ))}
                                </select>
                            </div>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={Data.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div>
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}