import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des actions correctives et préventives
export default function ListCorrectPrevent() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [correctPrevents, setCorrectPreventData] = React.useState([]);
    const fetchCorrectPrevent = () => {
        axios.get('/correctPrevents').then(response => {
            setCorrectPreventData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setCorrectPreventData([
            axios.get('/correctPrevents').then(response => {
                setCorrectPreventData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (correctPreventId) => {
        axios.delete(`/correctPrevents/${correctPreventId}`).then(response => {
            fetchCorrectPrevent();
        })
    }

     // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des corrective Preventive</h4>
        {correctPrevents.length === 0 ? "Pas de correctPrevent disponible" : null}
        <Link to="/AddCorrectPrevent" className="btn btn-outline-info btn-sm m-3">Ajouter une corrective Preventive</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>correctPrevent</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                       {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {correctPrevents.map(correctPrevent => (
                        <tr key={correctPrevent.id}>
                            <td>{correctPrevent.id}</td>
                            <td>{correctPrevent.correctPrevent}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{correctPrevent.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddCorrectPrevent/${correctPrevent.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(correctPrevent.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

