import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste du type d'at
export default function AddType() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { typeId } = useParams();
    const [typeData, setTypeData] = useState({});

    const [repartitions, setRepartitionData] = useState([]);
    const [checkboxData, setcheckboxData] = useState(false);

    const history = useHistory();

    //Méthode pour vérifier que le type d'at n'existe pas déjà
    useEffect(() => {
        axios.get('/repartitions').then(response => {
            setRepartitionData(response.data)
            setTypeData({
                origine: '',
                accOrigine: '',
                repartitionId: response.data[0].id,
                isAffiche: response.data.isAffiche,
            })
        }).then(() => {
            if (typeId) {
                axios.get(`/types/${typeId}`).then(response => {
                    console.log(response.data);
                    // console.log("repartion id " + response.data.repartition.id + " " + response.data.repartition.repartition)
                    setTypeData({
                        origine: response.data.origine,
                        accOrigine: response.data.accOrigine,
                        repartitionId: response.data.repartition.id,
                        isAffiche: response.data.isAffiche,
                    })
                })
            }
        })
    }, [typeId])

    //Méthode pour l'enregistrement du type d'at
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(typeData);
        if (typeId) {
            axios.put(`/types/${typeId}`, {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        } else {
            axios.post('/types', {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        }
    }

    const handleChange = (event) => {
        let currentState = { ...typeData };
        currentState[event.target.name] = event.target.value;
        setTypeData(currentState);
    }

    const handleCheckbox = (event) => {
        let currentState = { ...typeData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setTypeData(currentState);
    }

    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une type</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">origine</label>
                        <input type="text" name="origine" value={typeData.origine} onChange={handleChange} className="form-control" />
                        <label className="">accOrigine</label>
                        <input type="text" name="accOrigine" value={typeData.accOrigine} onChange={handleChange} className="form-control" />
                        <label className="">repartition </label>
                        <select name="repartitionId" value={typeData.repartitionId} onChange={handleChange} className="form-control" >
                            {
                                repartitions.map(repartition => (
                                    <option value={repartition.id} key={repartition.id} >{repartition.repartition}</option>
                                ))}
                        </select>
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={typeData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}