import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste du statut
export default function AddStatut() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { statutId } = useParams();
    const [statutData, setStatutData] = React.useState({ statut: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

     //Méthode pour vérifier que le statut n'existe pas déjà
    useEffect(() => {
        if (statutId) {
            axios.get(`/statuts/${statutId}`).then(response => {
                console.log(response.data);
                setStatutData({
                    statut: response.data.statut,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [statutId])

    //Méthode pour l'enregistrement du statut
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(statutData);
        if (statutId) {
            axios.put(`/statuts/${statutId}`, {
                ...statutData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListStatut')
            })
        } else {
            axios.post('/statuts', {
                ...statutData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListStatut')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...statutData };
        currentState[event.target.name] = event.target.value;
        setStatutData(currentState);
    }

// Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...statutData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setStatutData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une statut</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Statut</label>
                        <input type="text" name="statut" value={statutData.statut} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={statutData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

