import React from "react";
import { Link } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import Logo from "../../assets/images/logo.png";
import { useFormik } from 'formik'
import * as Yup from 'yup'
import { useHistory } from "react-router-dom";

//Classe pour s'inscrire sur l'application
export default function AddUser() {
    const history = useHistory();

    //Constante servant à initier les valeurs de bases pour la validation
    const initialValues = {
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        roleID: 2
    }
    //Constantes servant à la validation du formulaire avec Yup et Formik ainsi que les notifications si valeur incorrecte
    const validationSchema = Yup.object({
        firstName: Yup.string().required('Obligatoire').min(3, 'Le mot de passe doit comporter 3 caractères').max(50, 'trop long'),
        lastName: Yup.string().required('Obligatoire').min(3, 'Le mot de passe doit comporter 3 caractères').max(50, 'trop long'),
        email: Yup.string().email("Le format de l'email n'est pas bon").required('Obligatoire'),
        password: Yup.string().required('Obligatoire').matches(
            /^.*(?=.{15,})((?=.*[!@#$%^&*()\-_=+{};:,<.>]){1})(?=.*\d)((?=.*[a-z]){1})((?=.*[A-Z]){1}).*$/,
            "le mot de passe doit contenir 15 caractères + 1 caractère spécial + 1 majuscule + 1 minuscule + 1 chiffre"
        ),
    });
    const formik = useFormik({
        initialValues: initialValues,
        validationSchema: validationSchema,
        onSubmit: values => {
            // alert(JSON.stringify(values, null, 2));
            console.log(values)
            axios.post('/users', {
                firstName: values.firstName,
                lastName: values.lastName,
                email: values.email,
                password: values.password,
                roleID: values.roleID
            }).then(response => {
                history.push("/Login")
            })
        },
    });

     //méthode pour afficher le formulaire à l'écran
    return (
        <div className="container mt-3 ">
            <div className="d-flex justify-content-center">
                <h1 className="text-white">Bienvenue sur ESACP</h1>
            </div>
            <div className="d-flex justify-content-center">
                <div className="card bg-Light p-3 col-8 " >
                    <div className="d-flex justify-content-between">
                        <div className="d-flex justify-content-center m-1">
                            <img src={Logo} width="150" height="150" alt="Logo Delpharm" />
                        </div>
                        <div className="d-flex justify-content-center m-1">
                            <h1>Inscription</h1>
                        </div>
                    </div>
                    <form onSubmit={formik.handleSubmit} className="m-1">

                        <label htmlFor="lastName">Nom</label>
                        <input
                            id="lastName"
                            name="lastName"
                            type="text"
                            className="form-control"
                            onChange={formik.handleChange}
                            value={formik.values.lastName}
                        />
                        {formik.errors.lastName ?
                            <div className="text-danger font-weight-bold">{formik.errors.lastName}</div> : null}
                        <label htmlFor="firstName">Prénom</label>
                        <input
                            className="form-control"
                            id="firstName"
                            name="firstName"
                            type="text"
                            onChange={formik.handleChange}
                            value={formik.values.firstName}
                        />
                        {formik.errors.firstName ?
                            <div className="text-danger font-weight-bold">{formik.errors.firstName}</div> : null}

                        <label htmlFor="email">email</label>
                        <input
                            className="form-control"
                            id="email"
                            name="email"
                            type="email"
                            onChange={formik.handleChange}
                            value={formik.values.email}
                        />
                        {formik.errors.email ?
                            <div className="text-danger font-weight-bold">{formik.errors.email}</div> : null}
                        <label htmlFor="password">Mot de passe</label>
                        <input
                            className="form-control"
                            id="password"
                            name="password"
                            type="password"
                            onChange={formik.handleChange}
                            value={formik.values.password}
                        />
                        {formik.errors.password ?
                            <div className="text-danger font-weight-bold">{formik.errors.password}</div> : null}
                        <button type="submit" className="btn btn-primary mt-2 btn-lg col-12 mx-auto">Inscription</button>
                    </form>
                    <div class="d-flex justify-content-between">
                        <Link to="/Login" className="btn btn-info m-1 btn-lg col-5">Connexion</Link>
                        <Link to="/AddUser" className="btn btn-danger m-1 btn-lg col-6">Mot de passe perdu ?</Link>
                    </div>
                </div>
            </div>
        </div>

    )
}
