import React from "react";
import { Link, withRouter } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from "axios";
import TextField from '@material-ui/core/TextField';
import Logo from "../../assets/images/logo.png"
import "bootswatch/dist/lumen/bootstrap.min.css";

//Classe pour se connecter à l'application avec un compte existant
class Login extends React.Component {
    
    constructor() {
        super();
        this.state = { userData: {} }
        this.handleChange = this.handleChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    // Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    onSubmit(event) {
        event.preventDefault();
        axios.post('/authenticate', {
            email: this.state.userData.email,
            password: this.state.userData.password
        }).then((response) => {
            this.props.setUserInfo({ username: response.data.userName, role: response.data.roleID })
            console.log(response.data)
            console.log(response.data.roleID)
            this.props.history.push('/home')
        })
    }

     //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement du rapport en BDD
    handleChange(event) {
        let currentState = { ...this.state.userData };
        currentState[event.target.name] = event.target.value;
        this.setState({ userData: currentState });
    }

         //méthode pour afficher le formulaire à l'écran
    render() {
        return (
            <div className="container mt-3 ">
                <div className="d-flex justify-content-center">
                    <h1 className="text-white">Bienvenue sur ESACP</h1>
                </div>
                <div className="d-flex justify-content-center">
                    <div className="card bg-Light p-3 col-7 " >
                        <div className="d-flex flex-row align-items-center ">
                            <img src={Logo} width="200" height="200" alt="Logo Delpharm" className="m-3" />
                            <div className="d-flex d-flex flex-column align-items-center ml-3 ">
                                <form onSubmit={this.onSubmit} className="m-1">
                                    <h3 className="d-flex justify-content-center">Login</h3>
                                    <TextField variant="outlined" margin="normal" required fullWidth id="email" label="Email Address" name="email" autoComplete="email" autoFocus onChange={this.handleChange} />
                                    <TextField variant="outlined" margin="normal" required fullWidth name="password" label="Password" type="password" id="password" autoComplete="current-password" onChange={this.handleChange} />
                                    <button type="submit" className="btn btn-primary mt-2 btn-lg col-12 mx-auto">Connexion</button>
                                </form>

                                <Link to="/AddUser" className="btn btn-info btn-lg col-12 m-2 ">Inscription</Link>
                                <Link to="/AddUser" className="btn btn-warning btn-lg  col-12 m-2">Mot de passe perdu ?</Link>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default withRouter(Login)