import React, { useState, useEffect } from "react"
import { Link, useHistory } from "react-router-dom"
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import Logo from "../../assets/images/logo.png"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSignOutAlt, faHome } from "@fortawesome/free-solid-svg-icons";
import axios from 'axios';

//Classe pour la gestion de la navbar de l'application
export default function NavigationBar({ userInfo, setUserInfo }) {

// Constante pour l'affichage de la partie réservée à l'admin en fonction du rôle de l'utilisateur
  const [showPartieAdmin, setPartieAdmin] = useState(false);
  const history = useHistory();

  // Constante pour la déconnexion de l'utilisateur via axios
  const signout = () => {
    axios.post('/logout').then(response => {
      history.push('/Login')
      setUserInfo(null)
    })
  }

  //Méthode pour détecter si un utilisateur a le rôle admin et si oui, afficher la partie admin de la navbar
  useEffect(() => {
    if (userInfo.role === 1) {
      setPartieAdmin(true)
    }
  }, [userInfo.role])

  // Constante contenant l'html de la partie admin
  const PartieAdmin = () =>
    <div>
      <Nav>
        <Link to="/AdminUserList" className="nav-link">Gestions des utilisateurs</Link>
        <NavDropdown title="Parametre" id="basic-nav-dropdown">
          <NavDropdown.Item><Link to="/ListCategorie">Categorie</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListContrat">Contrat</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListCorrectPrevent">CorrectPrevent</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListCriticite">Criticite</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListCsp">Csp</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListDepartement">Departement</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListEquipement">Equipement</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListEtapeValidation">EtapeValidation</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListFicheSuiviSante">FicheSuiviSante</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListListingMail">ListingMail</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListNatureAccident">NatureAccident</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListOrigineLesions">OrigineLesions</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListPiecesJointes">PiecesJointes</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListPriorite">Priorite</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListRepartition">Repartition</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListSexe">Sexe</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListSiegeLesions">SiegeLesions</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListSite">Site</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListStatut">Statut</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListType">Type</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListTypeAt">TypeAt</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListTypeAction">TypeAction</Link></NavDropdown.Item>
          <NavDropdown.Item><Link to="/ListTypeRapport">TypeRapport</Link></NavDropdown.Item>
        </NavDropdown>
      </Nav>
    </div>;

// Return pour afficher la page à l'écran
  return (
    <div>
      <Navbar bg="dark" variant="dark">
        <Nav className="mr-auto">
          <img src={Logo} width="40" height="40" alt="Logo Delpharm" />
          <Link to="/home" className="text-primary ml-3 d-flex align-items-center p-2">
            <FontAwesomeIcon icon={faHome} size="lg" />
            {" "}</Link>
          {/* <Link to="/ATInterneEdit"> Edit ATInterne</Link> */}
          <NavDropdown title="Rapport" id="basic-nav-dropdown">
            <NavDropdown.Item><Link to="/AtChoix" >Ajouter un AT</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/AddPqa"> Ajouter un PQA/SD</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/AddAutreRapports">Ajouter un Rapport divers</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/ListRapport" >Liste des rapports</Link></NavDropdown.Item>
          </NavDropdown>
          <NavDropdown title="Action" id="basic-nav-dropdown">
            <NavDropdown.Item><Link to="/ListAction">Liste des Actions</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/ListActionFiltrer">Consulter action mois en cours et dépassées</Link></NavDropdown.Item>
          </NavDropdown>
          {/* Ligne de code contenant un booléen pour afficher ou masquer la partie admin en fonction du rôle*/}
          {showPartieAdmin ? <PartieAdmin /> : true}
          <NavDropdown title="Statistique" id="basic-nav-dropdown">
            <NavDropdown.Item><Link to="/Stats12MoisGlissant" >Stats sur 12 mois glissant</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/StatsSurAnnee">Stats sur l'annnée en cours</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/StatsAccidentologie">Stats sur l'accidentologie</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/StatsTFTG">Stats TF TG</Link></NavDropdown.Item>
            <NavDropdown.Item><Link to="/StatsRepartition">Stats par répartition</Link></NavDropdown.Item>
          </NavDropdown>
        </Nav>
        <div className="d-flex justify-content-end">
          <div className="nav-link text-white">Bienvenue, {userInfo.username} </div>
          <button variant="secondary" className="btn btn-outline-danger btn-sm" onClick={signout}> <FontAwesomeIcon icon={faSignOutAlt} size="sm" /></button>
        </div>
      </Navbar>
    </div>
  )
}