import React, { useState, useEffect } from 'react';
import axios from 'axios';
import MaterialTable from 'material-table'
import { forwardRef } from 'react';
import { ExportToExcel } from './ExportToExcel'
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import { useHistory, useParams, Link, Redirect } from 'react-router-dom';

//Classe pour afficher la liste des rapports
function ListRapport() {
    // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    const [selectedRow, setSelectedRow] = useState(null);
    const [listdata, setData] = useState([]);
    const [loadingData, setLoadingData] = useState(true);

       // Constante pour l'affichage des icônes présents sur le tableau
    const tableIcons = {
        Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
        Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
        Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
        DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
        Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
        Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
        FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
        LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
        NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
        ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
        SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
        ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
        ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
    };

      //Méthode pour récupérer les différentes données de la BDD via axios après chaque re-render
    useEffect(() => {
        async function getData() {
            await axios
                .get('/rapports')
                .then((response) => {
                    setData(response.data);
                    setLoadingData(false);
                });
        }
        if (loadingData) {
            getData();
        }
    }, []);

    const history = useHistory();

    // constante pour récupérer les valeurs de la BDD après modification de cette dernière
    const fetchData = () => {
        axios.get('/rapports')
            .then((response) => {
                setData(response.data)
            })
    }

      //constante pour supprimer une ligne du tableau
    const onRowDelete = oldData =>
        new Promise((resolve, reject) => {
            setTimeout(() => {
                {
                    let dataTemp = listdata;
                    const index = dataTemp.indexOf(oldData);
                    dataTemp.splice(index, 1);
                    setData(dataTemp);
                    console.log(dataTemp)
                    console.log(index)
                    console.log(oldData)
                    axios.delete(`/rapports/${oldData.id}`).then(response => {
                        fetchData();
                    })
                }
                resolve();
            }, 1000);
        });

          //Constante pour instantier les colonnes du tableau
    const tableRef = React.createRef();
    const [columns, setColumns] = useState(
        [
            { title: 'id', field: 'id', defaultSort: 'desc' },
            { title: 'Type de rapport', field: 'typeRapport.typeRapport' },
            { title: 'Date de création', field: 'dateDeCreation', type: "datetime", },
            { title: 'Rédacteur prénom', field: 'redacteur.firstName' },
            { title: 'Rédacteur Nom', field: 'redacteur.lastName' },
            { title: 'Victime Matricule', field: 'personneAT.matricule' },
            { title: 'Victime Prénom', field: 'personneAT.firstName' },
            { title: 'Victime Nom', field: 'personneAT.lastName' },
            { title: 'Connaissance AT', field: 'dateEtHeureConnaissanceAt', type: "datetime", },
            { title: 'Prévenu comment ?', field: 'prevenuComment' },
            { title: '1ère Personne prévenu', field: 'nomPremierePersonnePrevenu' },
            { title: 'Date et heure prévenu', field: 'dateEtHeurePrevenu', type: "datetime", },
            { title: 'Témoin ?', field: 'isTemoin' },
            { title: 'Commentaire témoin', field: 'commentaireTemoin' },
            { title: 'Tiers en cause ?', field: 'isTiersEnCause', type: "boolean", lookup: { true: 'Oui', false: 'Non' } },
            { title: 'Commentaire tiers en cause', field: 'commentaireTiersEnCause' },
            { title: 'commentaire autre victime', field: 'commentaireAutreVictime' },
            { title: 'Rapport de police ?', field: 'isRapportDePolice', type: "boolean", lookup: { true: 'Oui', false: 'Non' } },
            { title: 'Autre Victime ?', field: 'isAutreVictime', type: "boolean", lookup: { true: 'Oui', false: 'Non' } },
            { title: 'commentaire rapport de police', field: 'commentaireRapportDePolice' },
            { title: 'Victime Transports ?', field: 'isVictimeTransports', type: "boolean", lookup: { true: 'Oui', false: 'Non' } },
            { title: 'commentaire victime Transporter', field: 'commentaireVictimeTransporter' },
            { title: 'Moment accident', field: 'dateEtHeureMomentAccident', type: "datetime", },
            { title: "lieu de l'accident", field: 'lieuAccident' },
            { title: 'Du Identifier', field: 'isIdentifierDu', type: "boolean", lookup: { true: 'Oui', false: 'Non' } },
            { title: "circonstance de l'accident", field: 'circonstanceAccident' },
        ]);

        //Return pour afficher la page à l'écran
    return (
        <div>
            <div>
                <ExportToExcel classname="btn btn-success" apiData={listdata} fileName='Export XLS' />
            </div>
            <MaterialTable id="table-to-xls"
                title="Tableau rapport"
                tableRef={tableRef}
                columns={columns}
                data={listdata}
                onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
                options={{
                    search: true,
                    filtering: true,
                    exportButton: true,
                    grouping: true,
                    sorting: true,
                    paging: true,
                    maxBodyHeight: '900px',
                    padding: 'dense',
                    columnsButton: true,
                    actionsColumnIndex: 0,
                    detailPanelColumnAlignment: "right",
                    headerStyle: {
                        position: "sticky",
                        backgroundColor: "#01579b",
                        color: "#FFF",
                    },
                }}
                actions={[
                    {
                        icon: Edit,
                        tooltip: 'Editer',
                        onClick: (event, rowData) => {
                            console.log(rowData.id)
                            switch (rowData.typeRapport.id) {
                                // { `/AddType/${type.id}` }
                                case 1: return console.log(rowData.id), history.push({
                                    pathname: '/ATInterneEdit',
                                    state: { rapportId: rowData.id },
                                });
                                case 2: return console.log(rowData.id), history.push({
                                    pathname: '/AddPQAEdit',
                                    state: { rapportId: rowData.id },
                                });
                                case 3: return console.log(rowData.id), history.push({
                                    pathname: '/AddAutreRapportsEdit',
                                    state: { rapportId: rowData.id },
                                });
                                case 4: return console.log(rowData.id), history.push({
                                    pathname: '/ATExterneEdit',
                                    state: { rapportId: rowData.id },
                                });
                                default: return console.log(rowData.id), history.push({
                                    pathname: '/home',
                                    state: { rapportId: rowData.id },
                                });
                            }
                        }
                    },
                    {
                        icon: AddBox,
                        tooltip: 'Ajouter une Action',
                        onClick: (event, rowData) => {
                            return console.log(rowData.id), history.push({
                                pathname: '/AddAction',
                                state: { rapportId: rowData.id },
                            });
                        }
                    }
                ]}
                localization={{
                    body: {
                        emptyDataSourceMessage: "Pas d'enregistreent à afficher",
                        addTooltip: 'Ajouter',
                        deleteTooltip: 'Supprimer',
                        editTooltip: 'Editer',
                        filterRow: {
                            filterTooltip: 'Filtrer'
                        },
                        editRow: {
                            deleteText: 'Voulez-vous supprimer cette ligne?',
                            cancelTooltip: 'Annuler',
                            saveTooltip: 'Enregistrer'
                        }
                    },
                    grouping: {
                        placeholder: "Tirer l'entête ...",
                        groupedBy: 'Grouper par:'
                    },
                    header: {
                        actions: 'Actions'
                    },
                    pagination: {
                        labelDisplayedRows: '{from}-{to} de {count}',
                        labelRowsSelect: 'lignes',
                        labelRowsPerPage: 'lignes par page:',
                        firstAriaLabel: 'Première page',
                        firstTooltip: 'Première page',
                        previousAriaLabel: 'Page précédente',
                        previousTooltip: 'Page précédente',
                        nextAriaLabel: 'Page suivante',
                        nextTooltip: 'Page suivante',
                        lastAriaLabel: 'Dernière page',
                        lastTooltip: 'Dernière page'
                    },
                    toolbar: {
                        addRemoveColumns: 'Ajouter ou supprimer des colonnes',
                        nRowsSelected: '{0} ligne(s) sélectionée(s)',
                        showColumnsTitle: 'Voir les colonnes',
                        showColumnsAriaLabel: 'Voir les colonnes',
                        exportTitle: 'Exporter',
                        exportAriaLabel: 'Exporter',
                        exportName: 'Exporter en CSV',
                        searchTooltip: 'Chercher',
                        searchPlaceholder: 'Chercher'
                    }
                }}
                icons={tableIcons}
                editable={{
                    isDeletable: rowData => true,
                    onRowDelete: onRowDelete
                }}
            />
        </div>
    );
}

export default ListRapport;
