import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des fiches de suivi de santé
export default function ListFicheSuiviSante() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [ficheSuiviSantes, setFicheSuiviSanteData] = React.useState([]);
    const fetchFicheSuiviSante = () => {
        axios.get('/ficheSuiviSantes').then(response => {
            setFicheSuiviSanteData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setFicheSuiviSanteData([
            axios.get('/ficheSuiviSantes').then(response => {
                setFicheSuiviSanteData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (ficheSuiviSanteId) => {
        axios.delete(`/ficheSuiviSantes/${ficheSuiviSanteId}`).then(response => {
            fetchFicheSuiviSante();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des FicheSuiviSante</h4>
        {ficheSuiviSantes.length === 0 ? "Pas de ficheSuiviSante disponible" : null}
        <Link to="/AddFicheSuiviSante" className="btn btn-outline-info btn-sm m-3">Ajouter une ficheSuiviSante</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>ficheSuiviSante</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {ficheSuiviSantes.map(ficheSuiviSante => (
                        <tr key={ficheSuiviSante.id}>
                            <td>{ficheSuiviSante.id}</td>
                            <td>{ficheSuiviSante.ficheSuiviSante}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{ficheSuiviSante.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddFicheSuiviSante/${ficheSuiviSante.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(ficheSuiviSante.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

