import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des actions correctives préventives
export default function AddCorrectPrevent() {

      //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { correctPreventId } = useParams();
    const [correctPreventData, setCorrectPreventData] = React.useState({ correctPrevent: '' });
    const [checkboxData, setcheckboxData] = useState();
    const history = useHistory();

      //Méthode pour vérifier que l'action corrective préventive n'existe pas déjà
    useEffect(() => {
        if (correctPreventId) {
            axios.get(`/correctPrevents/${correctPreventId}`).then(response => {
                console.log(response.data);
                setCorrectPreventData({
                    correctPrevent: response.data.correctPrevent,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [correctPreventId])

      //Méthode pour l'enregistrement de l'action corrective préventive
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(correctPreventData);
        if (correctPreventId) {
            axios.put(`/correctPrevents/${correctPreventId}`, {
                ...correctPreventData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListCorrectPrevent')
            })
        } else {
            axios.post('/correctPrevents', {
                ...correctPreventData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListCorrectPrevent')
            })
        }
    }

     // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...correctPreventData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setCorrectPreventData(currentState);
    }
        //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...correctPreventData };
        currentState[event.target.name] = event.target.value;
        setCorrectPreventData(currentState);
    }

     //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une corrective Preventive</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">corrective Preventive</label>
                        <input type="text" name="correctPrevent" value={correctPreventData.correctPrevent} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={correctPreventData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

