import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des répartitions
export default function AddRepartition() {

     //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { repartitionId } = useParams();
    const [repartitionData, setRepartitionData] = React.useState({ repartition: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que la répartition n'existe pas déjà
    useEffect(() => {
        if (repartitionId) {
            axios.get(`/repartitions/${repartitionId}`).then(response => {
                console.log(response.data);
                setRepartitionData({
                    repartition: response.data.repartition,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [repartitionId])

    //Méthode pour l'enregistrement de la répartition
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(repartitionData);
        if (repartitionId) {
            axios.put(`/repartitions/${repartitionId}`, {
                ...repartitionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRepartition')
            })
        } else {
            axios.post('/repartitions', {
                ...repartitionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRepartition')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...repartitionData };
        currentState[event.target.name] = event.target.value;
        setRepartitionData(currentState);
    }

     // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...repartitionData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setRepartitionData(currentState);
    }

        //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une repartition</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Repartition</label>
                        <input type="text" name="repartition" value={repartitionData.repartition} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={repartitionData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />

                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

