import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des natures d'accident
export default function ListNatureAccident() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [natureAccidents, setNatureAccidentData] = React.useState([]);
    const fetchNatureAccident = () => {
        axios.get('/natureAccidents').then(response => {
            setNatureAccidentData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setNatureAccidentData([
            axios.get('/natureAccidents').then(response => {
                setNatureAccidentData(response.data)
            })
        ])
    }, [])

     //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (natureAccidentId) => {
        axios.delete(`/natureAccidents/${natureAccidentId}`).then(response => {
            fetchNatureAccident();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des NatureAccident</h4>
        {natureAccidents.length === 0 ? "Pas de natureAccident disponible" : null}
        <Link to="/AddNatureAccident" className="btn btn-outline-info btn-sm m-3">Ajouter une natureAccident</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>natureAccident</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                     {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {natureAccidents.map(natureAccident => (
                        <tr key={natureAccident.id}>
                            <td>{natureAccident.id}</td>
                            <td>{natureAccident.typeNatureAccident}</td>
                                {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{natureAccident.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddNatureAccident/${natureAccident.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(natureAccident.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

