import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste du type d'action
export default function AddTypeAction() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { typeActionId } = useParams();
    const [typeActionData, setTypeActionData] = React.useState({ typeAction: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

       //Méthode pour vérifier que le type d'action n'existe pas déjà
    useEffect(() => {
        if (typeActionId) {
            axios.get(`/typeActions/${typeActionId}`).then(response => {
                console.log(response.data);
                setTypeActionData({
                    typeAction: response.data.typeAction,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [typeActionId])

    //Méthode pour l'enregistrement du type d'action
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(typeActionData);
        if (typeActionId) {
            axios.put(`/typeActions/${typeActionId}`, {
                ...typeActionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListTypeAction')
            })
        } else {
            axios.post('/typeActions', {
                ...typeActionData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListTypeAction')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...typeActionData };
        currentState[event.target.name] = event.target.value;
        setTypeActionData(currentState);
    }

      // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...typeActionData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setTypeActionData(currentState);
    }

       //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une typeAction</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">TypeAction</label>
                        <input type="text" name="typeAction" value={typeActionData.typeAction} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={typeActionData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

