import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des sexes
export default function AddSexe() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { sexeId } = useParams();
    const [sexeData, setSexeData] = useState({ sexe: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que le sexe n'existe pas déjà
    useEffect(() => {
        if (sexeId) {
            axios.get(`/sexes/${sexeId}`).then(response => {
                console.log(response.data);
                setSexeData({
                    sexe: response.data.sexe,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [sexeId])

    //Méthode pour l'enregistrement deu sexe
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(sexeData);
        if (sexeId) {
            axios.put(`/sexes/${sexeId}`, {
                ...sexeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSexe')
            })
        } else {
            axios.post('/sexes', {
                ...sexeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSexe')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...sexeData };
        currentState[event.target.name] = event.target.value;
        setSexeData(currentState);
    }

     // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...sexeData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setSexeData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une sexe</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Sexe</label>
                        <input type="text" name="sexe" value={sexeData.sexe} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={sexeData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

