import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des statuts
export default function ListStatut() {

     // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [statuts, setStatutData] = React.useState([]);
    const fetchStatut = () => {
        axios.get('/statuts').then(response => {
            setStatutData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setStatutData([
            axios.get('/statuts').then(response => {
                setStatutData(response.data)
            })
        ])
    }, [])

    //Constante pour afficher les entrées de la liste après chaque re-render
    const handleDelete = (statutId) => {
        axios.delete(`/statuts/${statutId}`).then(response => {
            fetchStatut();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Statut</h4>
        {statuts.length === 0 ? "Pas de statut disponible" : null}
        <Link to="/AddStatut" className="btn btn-outline-info btn-sm m-3">Ajouter une statut</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>statut</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {statuts.map(statut => (
                        <tr key={statut.id}>
                            <td>{statut.id}</td>
                            <td>{statut.statut}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{statut.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddStatut/${statut.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(statut.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

