import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


// Page pour afficher la liste des catégories
export default function ListCategoriet() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [categories, setCategorietData] = React.useState([]);
    const fetchCategoriet = () => {
        axios.get('/categories').then(response => {
            setCategorietData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setCategorietData([
            axios.get('/categories').then(response => {
                setCategorietData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (CategorietId) => {
        axios.delete(`/categories/${CategorietId}`).then(response => {
            fetchCategoriet();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des categories</h4>
        {categories.length === 0 ? "Pas de Categoriet disponible" : null}
        <Link to="/AddCategorie" className="btn btn-outline-info btn-sm m-3">Ajouter une categorie</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>categories</th>
                        <th>Affiche/masqué</th>
                        <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {categories.map(categorie => (
                        <tr key={categorie.id}>
                            <td>{categorie.id}</td>
                            <td>{categorie.categorie}</td>
                            {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{categorie.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddCategorie/${categorie.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(categorie.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

