import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des natures d'accident
export default function AddNatureAccident() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { natureAccidentId } = useParams();
    const [natureAccidentData, setNatureAccidentData] = React.useState({ natureAccident: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que la nature d'accident n'existe pas déjà
    useEffect(() => {
        if (natureAccidentId) {
            axios.get(`/natureAccidents/${natureAccidentId}`).then(response => {
                console.log(response.data);
                setNatureAccidentData({
                    natureAccident: response.data.typeNatureAccident,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [natureAccidentId])

    //Méthode pour l'enregistrement de la nature d'accident
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(natureAccidentData);
        if (natureAccidentId) {
            axios.put(`/natureAccidents/${natureAccidentId}`, {
                ...natureAccidentData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListNatureAccident')
            })
        } else {
            axios.post('/natureAccidents', {
                ...natureAccidentData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListNatureAccident')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...natureAccidentData };
        currentState[event.target.name] = event.target.value;
        setNatureAccidentData(currentState);
    }

      // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...natureAccidentData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setNatureAccidentData(currentState);
    }

     //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une natureAccident</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">NatureAccident</label>
                        <input type="text" name="typeNatureAccident" value={natureAccidentData.typeNatureAccident} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={natureAccidentData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

