import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des étapes de validation
export default function ListEtapeValidation() {

     // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [etapeValidations, setEtapeValidationData] = React.useState([]);
    const fetchEtapeValidation = () => {
        axios.get('/etapeValidations').then(response => {
            setEtapeValidationData(response.data)
        })
    }

     //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setEtapeValidationData([
            axios.get('/etapeValidations').then(response => {
                setEtapeValidationData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (etapeValidationId) => {
        axios.delete(`/etapeValidations/${etapeValidationId}`).then(response => {
            fetchEtapeValidation();
        })
    }


    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des EtapeValidation</h4>
        {etapeValidations.length === 0 ? "Pas de etapeValidation disponible" : null}
        <Link to="/AddEtapeValidation" className="btn btn-outline-info btn-sm m-3">Ajouter une etapeValidation</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>etapeValidation</th>
                        <th>EDIT</th>
                        <th>DELETE</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {etapeValidations.map(etapeValidation => (
                        <tr key={etapeValidation.id}>
                            <td>{etapeValidation.id}</td>
                            <td>{etapeValidation.etapeValidation}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{etapeValidation.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddEtapeValidation/${etapeValidation.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(etapeValidation.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

