import axios from "axios";
import React, { useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des pièces jointes
export default function AddPieceJointes() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { pieceJointesId } = useParams();
    const [pieceJointesData, setPieceJointesData] = React.useState({ pieceJointes: '' });
    const history = useHistory();

    //Méthode pour vérifier que la pièce jointe n'existe pas déjà
    useEffect(() => {
        if (pieceJointesId) {
            axios.get(`/pieceJointess/${pieceJointesId}`).then(response => {
                console.log(response.data);
                setPieceJointesData({
                    pieceJointes: response.data.pieceJointes,
                })
            })
        }
    }, [pieceJointesId])

     //Méthode pour l'enregistrement de l'origine de la lésion
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(pieceJointesData);
        if (pieceJointesId) {
            axios.put(`/pieceJointess/${pieceJointesId}`, {
                ...pieceJointesData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListPieceJointes')
            })
        } else {
            axios.post('/pieceJointess', {
                ...pieceJointesData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListPieceJointes')
            })
        }
    }

        //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...pieceJointesData };
        currentState[event.target.name] = event.target.value;
        setPieceJointesData(currentState);
    }

     //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une pieceJointes</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">PieceJointes</label>
                        <input type="text" name="pieceJointes" value={pieceJointesData.pieceJointes} onChange={handleChange} className="form-control" />
                                                <div className="d-flex justify-content-end mt-3">
                        <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

