import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des sièges des lésions
export default function AddSiegeLesions() {

     //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { siegeLesionsId } = useParams();
    const [siegeLesionsData, setSiegeLesionsData] = React.useState({ typeSiegeDeLesions: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que le siège de la lésion n'existe pas déjà
    useEffect(() => {
        if (siegeLesionsId) {
            axios.get(`/siegeLesionss/${siegeLesionsId}`).then(response => {
                console.log(response.data);
                setSiegeLesionsData({
                    typeSiegeDeLesions: response.data.typeSiegeDeLesions,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [siegeLesionsId])

    //Méthode pour l'enregistrement du siège de la lésion
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(siegeLesionsData);
        if (siegeLesionsId) {
            axios.put(`/siegeLesionss/${siegeLesionsId}`, {
                ...siegeLesionsData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSiegeLesions')
            })
        } else {
            axios.post('/siegeLesionss', {
                ...siegeLesionsData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListSiegeLesions')
            })
        }
    }

     //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...siegeLesionsData };
        currentState[event.target.name] = event.target.value;
        setSiegeLesionsData(currentState);
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...siegeLesionsData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setSiegeLesionsData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une siegeLesions</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">SiegeLesions</label>
                        <input type="text" name="typeSiegeDeLesions" value={siegeLesionsData.typeSiegeDeLesions} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={siegeLesionsData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

