import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { G2, Chart, Tooltip, Interval, Axis, Coordinate, getTheme, Legend } from "bizcharts";

// Classe pour afficher les statistiques sur 12 mois glissants
export default function AddType() {

        // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    let { typeId } = useParams();
    const [typeData, setTypeData] = useState({});
    const [departements, setdepartementData] = useState([]);
    const [repartitions, setRepartitionData] = useState([]);
    const [correctPrevents, setCorrectPreventData] = React.useState([]);
    const history = useHistory();

    //Constantes avec des stats notées en dur pour donner un exemple visuel
    const data = [
        { name: 'Action Total', Mois: '2020/10', nombre: 18.9 },
        { name: 'Action Total', Mois: '2020/11', nombre: 28.8 },
        { name: 'Action Total', Mois: '2020/12', nombre: 39.3 },
        { name: 'Action Total', Mois: '2021/01', nombre: 81.4 },
        { name: 'Action Total', Mois: '2021/02', nombre: 47 },
        { name: 'Action Total', Mois: '2021/03', nombre: 20.3 },
        { name: 'Action Total', Mois: '2021/04', nombre: 24 },
        { name: 'Action Total', Mois: '2021/05', nombre: 35.6 },
        { name: 'Action Total', Mois: '2021/06', nombre: 47 },
        { name: 'Action Total', Mois: '2021/07', nombre: 20.3 },
        { name: 'Action Total', Mois: '2021/08', nombre: 0 },
        { name: 'Action Total', Mois: '2021/09', nombre: 35.6 },
        { name: 'Action close', Mois: '2020/10', nombre: 10 },
        { name: 'Action close', Mois: '2020/11', nombre: 20 },
        { name: 'Action close', Mois: '2020/12', nombre: 15 },
        { name: 'Action close', Mois: '2021/01', nombre: 70 },
        { name: 'Action close', Mois: '2021/02', nombre: 40 },
        { name: 'Action close', Mois: '2021/03', nombre: 15 },
        { name: 'Action close', Mois: '2021/04', nombre: 20 },
        { name: 'Action close', Mois: '2021/05', nombre: 5 },
        { name: 'Action close', Mois: '2021/06', nombre: 46 },
        { name: 'Action close', Mois: '2021/07', nombre: 19 },
        { name: 'Action close', Mois: '2021/08', nombre: 0 },
        { name: 'Action close', Mois: '2021/09', nombre: 31 },
    ];
    const data1 = [
        { item: "Action Total", percent: 0.80 },
        { item: "Action close", percent: 0.20 },
    ];

    const data5 = [
        { item: "actions closes avant la date d’échéance", percent: 0.95 },
        { item: "nombre de délai reporté", percent: 0.05 },
    ];

    const data6 = [
        { item: "Non-conformité Majeure", percent: 0.95 },
        { item: "Non-conformité mineure", percent: 0.05 },
        { item: "Haut potentiel de gravité", percent: 0.95 },
        { item: "Enseignement important", percent: 0.05 },
        { item: "Point sensible", percent: 0.95 },
        { item: "Point Perfectible", percent: 0.05 },
    ];

    const data7 = [
        { item: "Priorité 1", percent: 0.95 },
        { item: "Priorité 2", percent: 0.05 },
        { item: "Priorité 3", percent: 0.05 },
    ];

    const data2 = [
        { item: "Interne", percent: 0.95 },
        { item: "Externe", percent: 0.05 },
    ];

    const data4 = [
        { name: 'production', Mois: '2020/10', nombre: 18.9 },
        { name: 'production', Mois: '2020/11', nombre: 28.8 },
        { name: 'production', Mois: '2020/12', nombre: 39.3 },
        { name: 'production', Mois: '2021/01', nombre: 81.4 },
        { name: 'production', Mois: '2021/02', nombre: 47 },
        { name: 'production', Mois: '2021/03', nombre: 20.3 },
        { name: 'production', Mois: '2021/04', nombre: 24 },
        { name: 'production', Mois: '2021/05', nombre: 35.6 },
        { name: 'production', Mois: '2021/06', nombre: 47 },
        { name: 'production', Mois: '2021/07', nombre: 20.3 },
        { name: 'production', Mois: '2021/08', nombre: 0 },
        { name: 'production', Mois: '2021/09', nombre: 35.6 },
        { name: 'Supply Chain', Mois: '2020/10', nombre: 7 },
        { name: 'Supply Chain', Mois: '2020/11', nombre: 8 },
        { name: 'Supply Chain', Mois: '2020/12', nombre: 9 },
        { name: 'Supply Chain', Mois: '2021/01', nombre: 20 },
        { name: 'Supply Chain', Mois: '2021/02', nombre: 4 },
        { name: 'Supply Chain', Mois: '2021/03', nombre: 5 },
        { name: 'Supply Chain', Mois: '2021/04', nombre: 5 },
        { name: 'Supply Chain', Mois: '2021/05', nombre: 10 },
        { name: 'Supply Chain', Mois: '2021/06', nombre: 10 },
        { name: 'Supply Chain', Mois: '2021/07', nombre: 2 },
        { name: 'Supply Chain', Mois: '2021/08', nombre: 0 },
        { name: 'Supply Chain', Mois: '2021/09', nombre: 4 },
        { name: 'Supports', Mois: '2020/10', nombre: 1 },
        { name: 'Supports', Mois: '2020/11', nombre: 5 },
        { name: 'Supports', Mois: '2020/12', nombre: 4 },
        { name: 'Supports', Mois: '2021/01', nombre: 8 },
        { name: 'Supports', Mois: '2021/02', nombre: 7 },
        { name: 'Supports', Mois: '2021/03', nombre: 2 },
        { name: 'Supports', Mois: '2021/04', nombre: 2 },
        { name: 'Supports', Mois: '2021/05', nombre: 4 },
        { name: 'Supports', Mois: '2021/06', nombre: 5 },
        { name: 'Supports', Mois: '2021/07', nombre: 2 },
        { name: 'Supports', Mois: '2021/08', nombre: 0 },
        { name: 'Supports', Mois: '2021/09', nombre: 1 },
    ];


        // Méthode pour récupérer les valeurs de la victime sélectionnée après un re-render de la page
    useEffect(() => {
        setdepartementData([
            axios.get('/departements').then(response => {
                setdepartementData(response.data)
            })
        ])
        setCorrectPreventData([
            axios.get('/correctPrevents').then(response => {
                setCorrectPreventData(response.data)
            })
        ])
        axios.get('/repartitions/').then(response => {
            setRepartitionData(response.data)
            setTypeData({
                origine: '',
                accOrigine: '',
                repartitionId: response.data[0].id,
            })

        }).then(() => {
            if (typeId) {
                axios.get(`/types/${typeId}`).then(response => {
                    console.log(response.data);
                    // console.log("repartion id " + response.data.repartition.id + " " + response.data.repartition.repartition)
                    setTypeData({
                        origine: response.data.origine,
                        accOrigine: response.data.accOrigine,
                        repartitionId: response.data.repartition.id,
                        isAffiche: response.data.isAffiche,
                    })
                })
            }
        })
    }, [typeId])

      // Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(typeData);
        if (typeId) {
            axios.put(`/types/${typeId}`, {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        } else {
            axios.post('/types', {
                ...typeData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListType')
            })
        }
    }

 //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement du rapport en BDD
    const handleChange = (event) => {
        let currentState = { ...typeData };
        currentState[event.target.name] = event.target.value;
        setTypeData(currentState);
    }

    //Constantes pour le formatage des statistiques
    const colors = data.reduce((pre, cur, idx) => {
        pre[cur.item] = getTheme().colors10[idx];
        return pre;
    }, {});

    const cols = {
        percent: {
            formatter: (val) => {
                val = val * 100 + "%";
                return val;
            },
        },
    };

         //méthode pour afficher les statistiques à l'écran
    return (
        <div className="Container bg-light">
            <h3 className="text-center">Statistique sur 12 mois glissant</h3>
            <div className="d-flex justify-content-sm-center">
                <div className="m-2">
                    <label className="">Département</label>
                    <select name="departementId" value={departements.departementId} onChange={handleChange} className="form-control" >
                        <option value=""  >Tout</option>
                        {
                            departements.map(departement => (
                                <option value={departement.id} key={departement.id} >{departement.departement}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">pilote</label>
                    <select name="departementId" value={departements.departementId} onChange={handleChange} className="form-control" >
                        <option value=""  >Tout</option>
                        {
                            departements.map(departement => (
                                <option value={departement.id} key={departement.id} >{departement.departement}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">repartitions</label>
                    <select name="repartitionsId" value={repartitions.repartitionsId} onChange={handleChange} className="form-control" >
                        <option value="">Tout</option>
                        {
                            repartitions.map(repartition => (
                                <option value={repartition.id} key={repartition.id} >{repartition.repartition}</option>
                            ))}
                    </select>
                </div>
                <div className="m-2">
                    <label className="">Capa</label>
                    <select name="correctPreventId" value={correctPrevents.correctPreventId} onChange={handleChange} className="form-control" >
                        <option value="">Tout</option>
                        {
                            correctPrevents.map(correctPrevent => (
                                <option value={correctPrevent.id} key={correctPrevent.id} >{correctPrevent.correctPrevent}</option>
                            ))}
                    </select>
                </div>
            </div>
            <div className="col-12 mt-3">

                <label>Nombre total d’actions total sur 12 mois glissant par mois et nombre d’actions closes</label>
                <Chart className="m-3" height={300} padding="auto" data={data} autoFit Title="toto">
                    <Legend position='right' name="category" title="Nombre total d’actions total sur 12 mois glissant par mois et nombre d’actions closes" />
                    <Interval
                        adjust={[
                            {
                                type: 'dodge',
                                marginRatio: 0,
                            },
                        ]}
                        color="name"
                        position="Mois*nombre"
                    />
                    <Tooltip shared showTitle={true} />
                </Chart>

                <label>Pourcentage d’actions ouvertes/actions closes sur 12 mois glissant</label>
                <Chart className="m-3" height={300} data={data1} scale={cols} interactions={['element-active']} autoFit>
                    <Coordinate type="theta" radius={0.75} />
                    <Tooltip showTitle={false} />
                    <Axis visible={false} />
                    <Interval
                        position="percent"
                        adjust="stack"
                        color="item"
                        style={{
                            lineWidth: 1,
                            stroke: "#fff",
                        }}
                        label={[
                            "item",
                            (item) => {
                                return {
                                    offset: 20,
                                    content: (data) => {
                                        return `${data.item}\n ${data.percent * 100}%`;
                                    },
                                    style: {
                                        fill: colors[item],
                                    },
                                };
                            },
                        ]}
                    />
                </Chart>

                <label>Répartition des actions ouvertes/actions closes par service sur 12 mois glissant</label>
                <Chart className="m-3" height={300} padding="auto" data={data4} autoFit>
                    <Interval
                        adjust={[
                            {
                                type: 'dodge',
                                marginRatio: 0,
                            },
                        ]}
                        color="name"
                        position="Mois*nombre"
                    />
                    <Tooltip shared />
                </Chart>
                <label>Répartition des actions par origine</label>
                <Chart className="m-3" height={300} data={data2} scale={cols} interactions={['element-active']} autoFit>
                    <Coordinate type="theta" radius={0.75} />
                    <Tooltip showTitle={false} />
                    <Axis visible={false} />
                    <Interval
                        position="percent"
                        adjust="stack"
                        color="item"
                        style={{
                            lineWidth: 1,
                            stroke: "#fff",
                        }}
                        label={[
                            "item",
                            (item) => {
                                return {
                                    offset: 20,
                                    content: (data) => {
                                        return `${data.item}\n ${data.percent * 100}%`;
                                    },
                                    style: {
                                        fill: colors[item],
                                    },
                                };
                            },
                        ]}
                    />
                </Chart>
                <label>Pourcentage des actions closes avant la date d’échéance et nombre de délai reporté </label>
                <Chart className="m-3" height={300} data={data5} scale={cols} interactions={['element-active']} autoFit>
                    <Coordinate type="theta" radius={0.75} />
                    <Tooltip showTitle={false} />
                    <Axis visible={false} />
                    <Interval
                        position="percent"
                        adjust="stack"
                        color="item"
                        style={{
                            lineWidth: 1,
                            stroke: "#fff",
                        }}
                        label={[
                            "item",
                            (item) => {
                                return {
                                    offset: 20,
                                    content: (data) => {
                                        return `${data.item}\n ${data.percent * 100}%`;
                                    },
                                    style: {
                                        fill: colors[item],
                                    },
                                };
                            },
                        ]}
                    />
                </Chart>
                <label>Répartition des actions par criticité </label>
                <Chart className="m-3" height={300} data={data6} scale={cols} interactions={['element-active']} autoFit>
                    <Coordinate type="theta" radius={0.75} />
                    <Tooltip showTitle={false} />
                    <Axis visible={false} />
                    <Interval
                        position="percent"
                        adjust="stack"
                        color="item"
                        style={{
                            lineWidth: 1,
                            stroke: "#fff",
                        }}
                        label={[
                            "item",
                            (item) => {
                                return {
                                    offset: 20,
                                    content: (data) => {
                                        return `${data.item}\n ${data.percent * 100}%`;
                                    },
                                    style: {
                                        fill: colors[item],
                                    },
                                };
                            },
                        ]}
                    />
                </Chart>
                <label>Répartition des actions par priorité </label>
                <Chart className="m-3" height={300} data={data7} scale={cols} interactions={['element-active']} autoFit>
                    <Coordinate type="theta" radius={0.75} />
                    <Tooltip showTitle={false} />
                    <Axis visible={false} />
                    <Interval
                        position="percent"
                        adjust="stack"
                        color="item"
                        style={{
                            lineWidth: 1,
                            stroke: "#fff",
                        }}
                        label={[
                            "item",
                            (item) => {
                                return {
                                    offset: 20,
                                    content: (data) => {
                                        return `${data.item}\n ${data.percent * 100}%`;
                                    },
                                    style: {
                                        fill: colors[item],
                                    },
                                };
                            },
                        ]}
                    />
                </Chart>
            </div>
        </div>


    )
}

