import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des criticité
export default function ListCriticite() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [criticites, setCriticiteData] = React.useState([]);
    const fetchCriticite = () => {
        axios.get('/criticites').then(response => {
            setCriticiteData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setCriticiteData([
            axios.get('/criticites').then(response => {
                setCriticiteData(response.data)
            })
        ])
    }, [])

     //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (criticiteId) => {
        axios.delete(`/criticites/${criticiteId}`).then(response => {
            fetchCriticite();
        })
    }

     // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">des Liste des criticités</h4>
        {criticites.length === 0 ? "Pas de criticite disponible" : null}
        <Link to="/AddCriticite" className="btn btn-outline-info btn-sm m-3">Ajouter une criticite</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>criticite</th>
                        <th>accronyme</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {criticites.map(criticite => (
                        <tr key={criticite.id}>
                            <td>{criticite.id}</td>
                            <td>{criticite.criticite}</td>
                            <td>{criticite.criticiteAcronyme}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{criticite.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddCriticite/${criticite.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(criticite.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

