import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des types d'action
export default function ListTypeAction() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [typeActions, setTypeActionData] = React.useState([]);
    const fetchTypeAction = () => {
        axios.get('/typeActions').then(response => {
            setTypeActionData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setTypeActionData([
            axios.get('/typeActions').then(response => {
                setTypeActionData(response.data)
            })
        ])
    }, [])

    //Constante pour afficher les entrées de la liste après chaque re-render
    const handleDelete = (typeActionId) => {
        axios.delete(`/typeActions/${typeActionId}`).then(response => {
            fetchTypeAction();
        })
    }

     // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des TypeAction</h4>
        {typeActions.length === 0 ? "Pas de typeAction disponible" : null}
        <Link to="/AddTypeAction" className="btn btn-outline-info btn-sm m-3">Ajouter une typeAction</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>typeAction</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                        {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {typeActions.map(typeAction => (
                        <tr key={typeAction.id}>
                            <td>{typeAction.id}</td>
                            <td>{typeAction.typeAction}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{typeAction.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddTypeAction/${typeAction.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(typeAction.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

