import React from "react";
import { Link } from "react-router-dom"

//Page pour choisir entre remplir un formulaire d'AT sur site ou hors site
export default function FormulaireATChoix() {

  return (
    <>

      <div className="d-flex justify-content-center mt-5">
        <div className="card bg-lignt col-8">
          <div className="d-flex justify-content-center mt-5">
            <h2>Accident sur site ou hors site?</h2>
          </div>
          <div className="d-flex justify-content-center m-5">
            <Link to="/AtInterne" className="col-5 btn btn-success p-5 m-2">SUR SITE</Link>
            <Link to="/AtExterne" className="col-5 btn btn-success p-5 m-2">HORS SITE</Link>
          </div>
        </div>
      </div>
    </>
  );
}