import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des équipements
export default function AddEquipement() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { equipementId } = useParams();
    const [equipementData, setEquipementData] = React.useState({ equipement: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que l'équipement n'existe pas déjà
    useEffect(() => {
        if (equipementId) {
            axios.get(`/equipements/${equipementId}`).then(response => {
                console.log(response.data);
                setEquipementData({
                    equipement: response.data.equipement,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [equipementId])

       //Méthode pour l'enregistrement de l'équipement
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(equipementData);
        if (equipementId) {
            axios.put(`/equipements/${equipementId}`, {
                ...equipementData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListEquipement')
            })
        } else {
            axios.post('/equipements', {
                ...equipementData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListEquipement')
            })
        }
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...equipementData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setEquipementData(currentState);
    }

       //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...equipementData };
        currentState[event.target.name] = event.target.value;
        setEquipementData(currentState);
    }

       //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une equipement</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">Equipement</label>
                        <input type="text" name="equipement" value={equipementData.equipement} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={equipementData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

