import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des pièces jointes
export default function ListPiecesJointes() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [piecesJointess, setPiecesJointesData] = React.useState([]);
    const fetchPiecesJointes = () => {
        axios.get('/piecesJointess').then(response => {
            setPiecesJointesData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setPiecesJointesData([
            axios.get('/piecesJointess').then(response => {
                setPiecesJointesData(response.data)
            })
        ])
    }, [])

     //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (piecesJointesId) => {
        axios.delete(`/piecesJointess/${piecesJointesId}`).then(response => {
            fetchPiecesJointes();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des PiecesJointes</h4>
        {piecesJointess.length === 0 ? "Pas de piecesJointes disponible" : null}
        <Link to="/AddPiecesJointes" className="btn btn-outline-info btn-sm m-3">Ajouter une piecesJointes</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>piecesJointes</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {piecesJointess.map(piecesJointes => (
                        <tr key={piecesJointes.id}>
                            <td>{piecesJointes.id}</td>
                            <td>{piecesJointes.piecesJointes}</td>
                             {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{piecesJointes.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddPiecesJointes/${piecesJointes.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(piecesJointes.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

