import axios from "axios";
import React, { useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des listing mail
export default function AddListingMail() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { listingMailId } = useParams();
    const [listingMailData, setListingMailData] = React.useState({ listingMail: '' });
    const history = useHistory();

        //Méthode pour vérifier que le listing mail de santé n'existe pas déjà
    useEffect(() => {
        if (listingMailId) {
            axios.get(`/listingMails/${listingMailId}`).then(response => {
                console.log(response.data);
                setListingMailData({
                    listingMail: response.data.listingMail,
                })
            })
        }
    }, [listingMailId])

    //Méthode pour l'enregistrement du listing mail
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(listingMailData);
        if (listingMailId) {
            axios.put(`/listingMails/${listingMailId}`, {
                ...listingMailData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListListingMail')
            })
        } else {
            axios.post('/listingMails', {
                ...listingMailData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListListingMail')
            })
        }
    }

     //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...listingMailData };
        currentState[event.target.name] = event.target.value;
        setListingMailData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une listingMail</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">ListingMail</label>
                        <input type="text" name="listingMail" value={listingMailData.listingMail} onChange={handleChange} className="form-control" />
                                                <div className="d-flex justify-content-end mt-3">
                        <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

