import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


// Page pour afficher la liste des origines des lésions
export default function ListOrigineLesions() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [origineLesionss, setOrigineLesionsData] = React.useState([]);
    const fetchOrigineLesions = () => {
        axios.get('/origineLesionss').then(response => {
            setOrigineLesionsData(response.data)
        })
    }

     //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setOrigineLesionsData([
            axios.get('/origineLesionss').then(response => {
                setOrigineLesionsData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (origineLesionsId) => {
        axios.delete(`/origineLesionss/${origineLesionsId}`).then(response => {
            fetchOrigineLesions();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des OrigineLesions</h4>
        {origineLesionss.length === 0 ? "Pas de origineLesions disponible" : null}
        <Link to="/AddOrigineLesions" className="btn btn-outline-info btn-sm m-3">Ajouter une origineLesions</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>origineLesions</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {origineLesionss.map(origineLesions => (
                        <tr key={origineLesions.id}>
                            <td>{origineLesions.id}</td>
                            <td>{origineLesions.origineLesions}</td>
                            {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{origineLesions.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddOrigineLesions/${origineLesions.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(origineLesions.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

