import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des sexes
export default function ListSexe() {

     // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [sexes, setSexeData] = React.useState([]);
    const fetchSexe = () => {
        axios.get('/sexes').then(response => {
            setSexeData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setSexeData([
            axios.get('/sexes').then(response => {
                setSexeData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (sexeId) => {
        axios.delete(`/sexes/${sexeId}`).then(response => {
            fetchSexe();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Sexe</h4>
        {sexes.length === 0 ? "Pas de sexe disponible" : null}
        <Link to="/AddSexe" className="btn btn-outline-info btn-sm m-3">Ajouter une sexe</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>sexe</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                     {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {sexes.map(sexe => (
                        <tr key={sexe.id}>
                            <td>{sexe.id}</td>
                            <td>{sexe.sexe}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{sexe.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddSexe/${sexe.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(sexe.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

