import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste du type de rapport
export default function AddTypeRapport() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { typeRapportId } = useParams();
    const [typeRapportData, setTypeRapportData] = React.useState({ typeRapport: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que le type de rapport n'existe pas déjà
    useEffect(() => {
        if (typeRapportId) {
            axios.get(`/typeRapports/${typeRapportId}`).then(response => {
                console.log(response.data);
                setTypeRapportData({
                    typeRapport: response.data.typeRapport,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [typeRapportId])

     //Méthode pour l'enregistrement du type de rapport
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(typeRapportData);
        if (typeRapportId) {
            axios.put(`/typeRapports/${typeRapportId}`, {
                ...typeRapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListTypeRapport')
            })
        } else {
            axios.post('/typeRapports', {
                ...typeRapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListTypeRapport')
            })
        }
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans les inputs
    const handleChange = (event) => {
        let currentState = { ...typeRapportData };
        currentState[event.target.name] = event.target.value;
        setTypeRapportData(currentState);
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...typeRapportData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setTypeRapportData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter une typeRapport</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">TypeRapport</label>
                        <input type="text" name="typeRapport" value={typeRapportData.typeRapport} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={typeRapportData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

