import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des catégories
export default function Addcategorie() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur.
    let { categorieId } = useParams();
    const [categorieData, setcategorieData] = React.useState({ categorie: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que la catégorie n'existe pas déjà
    useEffect(() => {
        if (categorieId) {
            axios.get(`/categories/${categorieId}`).then(response => {
                console.log(response.data);
                setcategorieData({
                    categorie: response.data.categorie,
                    isAffiche: response.data.isAffiche,
                })
               })
        }
    }, [categorieId])

    //Méthode pour l'enregistrement de la catégorie
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(categorieData);
        if (categorieId) {
            axios.put(`/categories/${categorieId}`, {
                ...categorieData
            }).then(() => {
                // rediriger vers la liste
                history.push('/Listcategorie')
            })
        } else {
            axios.post('/categories', {
                ...categorieData
            }).then(() => {
                // rediriger vers la liste
                history.push('/Listcategorie')
            })
        }
    }

 // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...categorieData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setcategorieData(currentState);
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...categorieData };
        currentState[event.target.name] = event.target.value;
        setcategorieData(currentState);
    }

    //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter un categorie</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-4">
                    <form onSubmit={onSubmit}>
                        <label className="">categorie</label>
                        <input type="text" name="categorie" value={categorieData.categorie} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={categorieData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <div className="d-flex justify-content-end mt-3">
                                <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

