import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des sièges de lésions
export default function ListSiegeLesions() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [siegeLesionss, setSiegeLesionsData] = React.useState([]);
    const fetchSiegeLesions = () => {
        axios.get('/siegeLesionss').then(response => {
            setSiegeLesionsData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setSiegeLesionsData([
            axios.get('/siegeLesionss').then(response => {
                setSiegeLesionsData(response.data)
                console.log("in the use effect siege lesion !" + response.data)
            })
        ])
    }, [])

    //Constante pour afficher les entrées de la liste après chaque re-render
    const handleDelete = (siegeLesionsId) => {
        axios.delete(`/siegeLesionss/${siegeLesionsId}`).then(response => {
            fetchSiegeLesions();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des SiegeLesions</h4>
        {siegeLesionss.length === 0 ? "Pas de siegeLesions disponible" : null}
        <Link to="/AddSiegeLesions" className="btn btn-outline-info btn-sm m-3">Ajouter une siegeLesions</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>siegeLesions</th>
                        <th>value</th>
                        <th>label</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {siegeLesionss.map(siegeLesions => (
                        <tr key={siegeLesions.id}>
                            <td>{siegeLesions.id}</td>
                            <td>{siegeLesions.value}</td>
                            <td>{siegeLesions.label}</td>
                              {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{siegeLesions.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddSiegeLesions/${siegeLesions.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(siegeLesions.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

