import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des répartitions
export default function ListRepartition() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [repartitions, setRepartitionData] = React.useState([]);
    const fetchRepartition = () => {
        axios.get('/repartitions').then(response => {
            setRepartitionData(response.data)
        })
    }

    //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setRepartitionData([
            axios.get('/repartitions').then(response => {
                setRepartitionData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (repartitionId) => {
        axios.delete(`/repartitions/${repartitionId}`).then(response => {
            fetchRepartition();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Repartition</h4>
        {repartitions.length === 0 ? "Pas de repartition disponible" : null}
        <Link to="/AddRepartition" className="btn btn-outline-info btn-sm m-3">Ajouter une repartition</Link>
        <div className="card">
            {/* <select name="repartition" value={repartitions.repartition} className="form-control">
                        {repartitions.map(repartition => (
                            <option value={repartition.id} key={repartition.id}>{repartition.repartition}</option>
                        ))}
                    </select>     */}
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>repartition</th>
                        <th>Affiche/masqué</th>
                        <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                     {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {repartitions.map(repartition => (
                        <tr key={repartition.id}>
                            <td>{repartition.id}</td>
                            <td>{repartition.repartition}</td>
                               {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{repartition.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddRepartition/${repartition.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(repartition.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

