import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Page pour afficher la liste des Départements
export default function Listdepartement() {

    // Constantes pour récuperer les valeurs de la liste depuis la BDD
    const [departements, setdepartementData] = React.useState([]);
    const fetchdepartement = () => {
        axios.get('/departements').then(response => {
            setdepartementData(response.data)
        })
    }

     //Constante pour afficher les entrées de la liste après chaque re-render
    React.useEffect(() => {
        setdepartementData([
            axios.get('/departements').then(response => {
                setdepartementData(response.data)
            })
        ])
    }, [])

    //Constante pour supprimer une entrée de la liste en BDD
    const handleDelete = (departementId) => {
        axios.delete(`/departements/${departementId}`).then(response => {
            fetchdepartement();
        })
    }

    // Return pour afficher la page de la liste
    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des departements</h4>
        {departements.length === 0 ? "Pas de departement disponible" : null}
        <Link to="/Adddepartement" className="btn btn-outline-info btn-sm m-3">Ajouter un departement</Link>
        <div className="card">
            <table className="table table-hover table-sm" data-toggle="table" data-search="true" data-show-columns="true">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>departement</th>
                        <th>type departement</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {/* le .map permet de faire une boucle pour afficher les entrées de la liste ligne par ligne */}
                    {departements.map(departement => (
                        <tr key={departement.id}>
                            <td>{departement.id}</td>
                            <td>{departement.departement}</td>
                            <td>{departement.typeService}</td>
                            {/* le .isAffiche est un booléen pour indiquer si une entrée est visible ou non par les utilisateurs non admin */}
                            <td>{departement.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/Adddepartement/${departement.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(departement.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

