import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams, Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave, } from "@fortawesome/free-solid-svg-icons";

//Classe pour l'ajout d'un autre rapport
export default function AddAutreRapport() {

    // Constantes servant pour récuperer les valeurs de la BDD et manipuler les valeurs rentrées dans le formulaire
    let { rapportId } = useParams();
    const [rapportData, setRapportData] = useState({ rapport: '', dateDeCreation: '', id: '', typeRapportID: 3 });
    const [categories, setCategorieData] = React.useState([]);
    const [types, setTypeData] = React.useState([]);
    const history = useHistory();

      // Méthode pour récupérer les valeurs de la victime sélectionnée après un re-render de la page
    useEffect(() => {
        axios.get('/categories').then(response => {
            setCategorieData(response.data)
        })
            .then(() => {
                axios.get('/types').then(response => {
                    setTypeData(response.data)
                })
                    .then(() => {
                        if (rapportId) {
                            axios.get(`/rapports/${rapportId}`).then(response => {
                                console.log(response.data);
                                setRapportData({
                                    rapport: response.data.rapport,
                                    dateDeCreation: response.data.dateDeCreation
                                })
                            })
                        }
                    })
            })
    }, [rapportId])

// Méthode pour enregistrer le formulaire en BDD et renvoyer l'utilisateur vers la liste des rapports
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(rapportData);
        if (rapportId) {
            axios.put(`/rapports/${rapportId}`, {
                ...rapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRapport')
            })
        } else {
            axios.post('/rapports', {
                ...rapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRapport')
            })
        }
    }

    //Méthode pour sauvegarder les valeurs saisies par l'utilisateur dans les champs avant enregistrement du rapport en BDD
    const handleChange = (event) => {
        let currentState = { ...rapportData };
        currentState[event.target.name] = event.target.value;
        setRapportData(currentState);
        console.log(currentState);

    }

     //méthode pour afficher le formulaire à l'écran
    return (
        <div className="d-flex justify-content-center mt-3">
            <div className="col-8">
                <div className="card bg-light">
                    <h2 className="bg-info text-center fw-bold">Autre rapport</h2>
                    <form onSubmit={onSubmit} className=" m-3">

                        <div className="d-flex jsutify-content-center">
                            <div className="d-flex jsutify-content-center">
                                <div className="mr-3">
                                    <label className="">Categorie</label>
                                    <select name="categorieID" value={categories.categorie} onChange={handleChange} className="form-control">
                                        {categories.map(categorie => (
                                            <option value={categorie.id} key={categorie.id}>{categorie.categorie}</option>
                                        ))}
                                    </select>
                                </div>
                            </div>
                            <div className="d-flex jsutify-content-center">
                                <div>
                                    <label className="">Type</label>
                                    <select name="typeID" value={types.type} onChange={handleChange} className="form-control">
                                        {types.map(type => (
                                            <option value={type.id} key={type.id}>{type.origine} | {type.accOrigine}</option>
                                        ))}
                                    </select>
                                </div>
                            </div>
                        </div>
                        <label className="">Description du rapport</label>
                        <textarea className="form-control" name="descriptionsituationdangereuse" value={rapportData.descriptionsituationdangereuse} onChange={handleChange} placeholder="Description du rapport"></textarea>
                        <label className="">Liste des pièces jointes</label>
                        <div class="input-group mb-3">
                            <label class="input-group-text" for="inputGroupFile01">Upload</label>

                            <input type="file" class="form-control" name="pjAutreRapport" value={rapportData.pjAutreRapport}
                            />

                        </div>
                        <div className="d-flex justify-content-end">
                            <button type="submit" className="btn btn-primary mb-3" ><FontAwesomeIcon icon={faSave} /></button>
                        </div>
                    </form>
                </div>
            </div>
        </div>

    )
}