import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

//Classe pour l'ajout d'une entrée dans la liste des départements
export default function AddDepartement() {

    //Constantes pour la gestion des valeurs rentrées par l'utilisateur
    let { departementId } = useParams();
    const [departementData, setDepartementData] = React.useState({ departement: '' });
    const [checkboxData, setcheckboxData] = useState(false);
    const history = useHistory();

    //Méthode pour vérifier que le département n'existe pas déjà
    useEffect(() => {
        if (departementId) {
            axios.get(`/departements/${departementId}`).then(response => {
                console.log(response.data);
                setDepartementData({
                    departement: response.data.departement,
                    typeService: response.data.typeService,
                    isAffiche: response.data.isAffiche,
                })
            })
        }
    }, [departementId])

    //Méthode pour l'enregistrement du département
    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(departementData);
        if (departementId) {
            axios.put(`/departements/${departementId}`, {
                ...departementData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListDepartement')
            })
        } else {
            axios.post('/departements', {
                ...departementData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListDepartement')
            })
        }
    }

    // Méthode pour la gestion des états coché/décochés des checkboxs
    const handleCheckbox = (event) => {
        let currentState = { ...departementData };
        setcheckboxData(!checkboxData);
        currentState[event.target.name] = !checkboxData;
        console.log(currentState);
        setDepartementData(currentState);
    }

    //Méthode pour la gestion des valeurs rentrés par l'utilisateur dans le inputs
    const handleChange = (event) => {
        let currentState = { ...departementData };
        currentState[event.target.name] = event.target.value;
        setDepartementData(currentState);
    }

        //Méthode pour afficher la fenêtre d'ajout
    return (
        <div className="Container">
            <h2 className="text-white text-center m-3">Ajouter un département</h2>
            <div className="d-flex justify-content-sm-center ">
                <div className="card col-6">
                    <form onSubmit={onSubmit}>
                        <label Htmlfor="basic-url" class="form-label">Departement</label>
                        <input type="text" name="departement" value={departementData.departement} onChange={handleChange} className="form-control" />
                        <label Htmlfor="basic-url" class="form-label">Type du service</label>
                        <input type="text" name="typeService" value={departementData.typeService} onChange={handleChange} className="form-control" />
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" name="isAffiche" id="flexCheckDefault" onChange={handleCheckbox} checked={checkboxData} value={departementData.isAffiche} />
                            <label class="form-check-label" for="flexCheckDefault">
                                Affiché/masqué
                            </label>
                        </div>
                        <div className="d-flex justify-content-end mt-3">
                            <div className="d-flex justify-content-end mt-3">
                                <input type="submit" className="btn btn-sm btn-primary" value="Enregistrer" />
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

